<!--
@component Round button (empty).

   Main properties:
   - `title` - title (alternative text).
   - `className` - additional class name for styling the button.
   - `onclick` - function (callback) to be called when user clicks on the button.
-->
<script>
   let {title, className, disable = false, onclick} = $props();
</script>

<button tabindex="0" {onclick} {title} aria-label={title} alt={title} class="button {className}"></button>

<style>
   button {
      display: inline-block;
      font-size: 1em;
      width: 1.3em;
      height: 1.3em;
      border: none;
      border-radius: 50%;
      box-shadow: none;
      margin: 0 0.25em;
      text-align: center;
      padding: 0;
      background-size: 45% 45%;
      background-position: 50%;
      background-repeat: no-repeat;
   }

   button:last-of-type {
      margin-right: 0;
   }

   .button:focus-visible {
      outline: solid 2px;
      outline-offset: 2px;
   }

</style>
