<script>
   /**************************************************************/
   /* DataLoader for Preprocessing app                           */
   /* --------------------------------                           */
   /* Loads raw dataset from CSV or SPC file                     */
   /* if model is provided, applies it and returns 'prepdata'    */
   /**************************************************************/

   import { FileSelector, Switch, Spinner, Select, Container, Button, TextInput } from 'svelte-controls-basic';
   import { cloneFullData, downloadPrepData } from '../utils.js';
   import { parseCSV, parseCSVParams, readSPCFiles } from '../../../shared/utils.js';
   import { prep_apply, prep_trim } from '../../../shared/wasm/dist/prep.js';

   import DatasetWidget from '../../../shared/widgets/DatasetWidget.svelte';

   let {
      WASM = null,
      model = null,                     // preprocessing model
      rawdata = $bindable(null),        // JSON with loaded dataset
      prepdata = $bindable(null),       // JSON with preprocessed dataset
      title = 'Raw data',
      left = null,
      right = null,
      multiple = true,
   } = $props();

   // possible delimeters and header options
   const sepOptions = [',', ';'];
   const headerOptions = ['no', 'labels', 'values'];

   // data properties
   let dataFileType = $state();          // type of data file
   let dataFileNum = $state(0);          // number of provided data files
   let dataFile = $state();              // CSV or SPC file
   let varvaluesName = $state('');       // name of the variable values to be used as header
   let varvaluesUnits = $state('');      // units of the variable values
   let firstColumn = $state('data');     // what is located in the first column ('data', 'groups', 'response')

   // data properties, that will be autodetected from CSV file
   let sep = $state(',');                // seperator between values
   let header = $state('no');            // first row contains header (labels or values)
   let hasLabels = $state(false);        // first column contains row labels
   let hasClassLabels = $state(false);   // if data file contains class labels

   // promise, app status for handling and showing errors
   let promise = $state(null);
   let status = $state('neutral');
   let errMsg = $state('');


   // method for showing error
   function setError(msg) {
      status = 'error';
      errMsg = msg;
      rawdata = null;
      console.error(msg)
   }

   // method for setting neutral state
   function setNeutral() {
      status = 'neural';
      errMsg = '';
   }

   // method for setting success state
   function setSuccess() {
      status = 'success';
      errMsg = '';
   }

   // reset whole component to initial state of the component
   function reset () {
      dataFile = '';
      rawdata = null;
      prepdata = null;
      varvaluesName = '';
      varvaluesUnits = '';
      errMsg = '';
      setNeutral();
   }


   // method to apply preprocessing model
   function predict() {
      if (!rawdata || !model) return;

      if (rawdata.values.ncols - rawdata.nresp !== model.npred) {
         setError(`Number of variables in the dataset (${rawdata.values.ncols}) does not match the expected number (${model.npred}).`);
         return;
      }

      promise = WASM()
         .then(Module => {
            status = 'applying';
            // fit model and add extra parameters
            const out = prep_apply(Module, rawdata.values, rawdata.nresp, model, false, false);
            // set global variables and status of the component
            const pd = cloneFullData($state.snapshot(rawdata), out);
            prepdata = prep_trim(pd, model);
            setSuccess();
         })
         .catch(e =>{
            // something went wrong
            prepdata = null;
            setError(e);
         });
   };

   // guess dataset parameters by reading first 100 kB of CSV file
   function guessParams(dataFile) {

      // no data file is provided - no guessing
      if (!dataFile || dataFile.size < 10) return;

      // read first 100Kb
      const chunkSize = 100000;
      const first100KB = dataFile.slice(0, chunkSize);

      const reader = new FileReader();
      reader.onload = () => {
         const csvString = reader.result;
         if (!csvString || csvString.length < 0) return;
         [sep, hasLabels, hasClassLabels, header] = parseCSVParams(csvString);
         if (hasClassLabels) firstColumn = 'groups';
      };

      reader.onerror = function(e) {
         csvString = null;
         setError(e)
      };

      reader.readAsText(first100KB);
   }

   // Gets CSV file content as string and creates dataset object.
   function setData(strData) {
      try{
         const d = parseCSV(
            strData,
            sep,
            header,
            hasLabels,
            firstColumn === 'groups',              // hasClassNames
            varvaluesName,
            varvaluesUnits,
            firstColumn === 'response' ? 1 : 0     // nresp
         );
         if (d.values.nrows < 1 ) {
            throw new Error('Dataset must have at least 1 row.');
         }

         if (d.classNames.length > d.values.nrows / 2) {
            throw new Error('Dataset has too many unique ' + className.toLowerCase() + ' labels (' + d.classNames.length + '), check your CSV file.');
         }

         const npred = d.values.ncols - d.nresp;
         if (model && npred !== model.npred) {
            throw new Error('Number of predictors in dataset (' + npred + ') does not match what model expects (' + model.npred + ')');
         }

         d['fileName'] = dataFile.name.replace(/\.[^/.]+$/, "");
         rawdata = d;
         setSuccess();
      } catch (e) {
         setError(e);
      }
   }

   // promise for reading the CSV file
   function readCSV (e) {
      const reader = new FileReader();
      reader.onload = () => { setData(reader.result) };
      reader.readAsText(dataFile);
   }

   // promise for reading the SPC file
   async function readSPC (dataFiles) {
      try {
         const dataFiles = dataFile instanceof FileList ? dataFile : [dataFile];
         rawdata = await readSPCFiles(dataFiles);
         setSuccess();
      } catch (err) {
         setError('Failed to read SPC file: ' + err);
      }
   }

   // if data file is selected - guess parameters
   $effect(() => {

      if (dataFile === undefined) return;

      // if data file is empty reset status and error messages
      if (dataFile === null || dataFile === '') {
         reset();
         return;
      }

      // dataFile is array with several SPC files
      if (Array.isArray(dataFile)) {

         if (dataFile.filter(v => v.slice(-4) === '.spc').length !== dataFile.length) {
            setError('Multiple file selection is allowed only for SPC files.');
            return;
         }

         readSPC(dataFile);
         return;
      }

      if (dataFile && (dataFile.type === 'text/csv' || dataFile.type === 'application/vnd.ms-excel')) {
         // if not just remove error messages if any and guess the parameters
         guessParams(dataFile);
         setNeutral();
      } else {
         readSPC([dataFile]);
      }
   });

   // if separator was changed remove error message
   $effect(() => {
      if (sep) {
         status = 'neutral';
         errMsg = '';
      }
   });

   // if dataset and model have been changed, make new predictions
   $effect(() => {
      if (rawdata !== null && model !== null && !prepdata && errMsg === '') {
         predict();
      }
   });

   $effect(() => {
      if (rawdata === null || rawdata === 0) dataFile = '';
   });
</script>

{#if !rawdata }

   <!-- show if data is not loaded -->
   <!-- reset component if dataFile is changed inside -->
   {#key dataFile === undefined}
   <Container>
      <FileSelector
         id="dataFile"
         message="Select CSV or SPC file with dataset"
         acceptType={'.csv, .spc'}
         multiple={true}
         bind:file={dataFile}
      />
   </Container>
   {/key}

   <!-- show if file is selected but data is not loaded -->
   {#if dataFile && !Array.isArray(dataFile) && (dataFile.type === 'text/csv' || dataFile.type === 'application/vnd.ms-excel')}
   <Container label="Delimiter">
      <Select id="sep" bind:value={sep} options={sepOptions} />
   </Container>
   <Container label="Row labels">
      <Switch id="hasLabels" bind:value={hasLabels} options={['no', 'yes']} />
   </Container>
   <Container label="First column">
      <Select id="sep" bind:value={firstColumn} options={['data', 'groups', 'response']} />
   </Container>
   <Container label="Header">
      <Select id="header" bind:value={header} options={headerOptions} />
   </Container>
   {#if header==='values'}
   <Container label="Header name">
      <TextInput id="varvaluesName" bind:value={varvaluesName} placeholder="e.g. Wavelength" />
   </Container>
   <Container label="Header units">
      <TextInput id="varvaluesUnits" bind:value={varvaluesUnits} maxLength={10} placeholder="e.g. nm" />
   </Container>
   {/if}
   <Container label="&nbsp;">
      <Button text='Load' onclick={readCSV}/>
   </Container>
   {/if}

{:else}


   {#if promise}

      <!-- calculation is in process -->
      {#await promise}
         <Container>
            <Spinner />
            <p>Loading data...</p>
         </Container>

      {:catch error}
         <!-- error message if failed -->
         <Container status="error">
            <p>{error}</p>
         </Container>
      {/await}

   {/if}

{/if}

{#if rawdata }
   <!-- dataset is loaded - show info and cancel button -->
   <DatasetWidget dataset={rawdata} {reset} {title} />
{/if}
{#if prepdata && model}
   <Container label="Download">
      <Button text="Preprocessed data" onclick={() => downloadPrepData(prepdata, left, right)}/>
   </Container>
{/if}

<!-- error message -->
{#if status === 'error' || errMsg}
<Container status="error">
   <p>{errMsg}</p>
</Container>
{/if}

