<script>
   /*************************************************************/
   /* Widget for scaling                                        */
   /* -----------------------------------                       */
   /*************************************************************/

   import { Container, Select, Range, RangeDiscrete } from 'svelte-controls-basic';
   import PrepTemplate from './PrepTemplate.svelte';

   let {
      params = $bindable([0, 1, 0.1]),
      varind = $bindable(-1),
      settings = $bindable(''),
      nvars = 0,
      ...others
   } = $props();

   // constant parameters
   const title = 'Baseline correction';
   const typesMap = {'emsc': 0, 'als': 1};
   const types = Object.keys(typesMap);

   // helper function to get lower boundary of penalty interval
   function lowerBoundary(x) {
      const precision = Math.ceil(Math.log10(x) - 1);
      return Math.pow(10, precision);
   }

   // helper function to get upper boundary of penalty interval
   function upperBoundary(x) {
      const precision = Math.ceil(Math.log10(x));
      return Math.pow(10, precision);
   }


   // initial settings
   let type = $state(types[params[0]]);
   let maxPenalty = $state(params[2] > 0 ? upperBoundary(params[2]).toString() : "0.1");

   // derived settings
   let maxPenaltyNum = $derived(parseFloat(maxPenalty));
   let stepPenalty = $derived(maxPenaltyNum / 10);
   let decNumPenalty = $derived(Math.ceil(-Math.log10(maxPenaltyNum)) + 1);


   // effect to correct params[2] when user changed penalty range
   $effect(() => {
      if (type !== 'als') return;
      const mp = parseFloat(maxPenalty);
      if (params[2] < stepPenalty || params[2] > mp - stepPenalty) {
         params[2] = stepPenalty;
      }
   });

   // effect for making settings string
   $effect(() => {settings = type + ' (' + (type == 'emsc' ? params[1].toFixed(0) : params[1].toFixed(1) + ',' + params[2].toFixed(3))  + ')';});

   // effect to trace changes in type
   $effect(() => { if (type) params[0] = typesMap[type]});
</script>


<PrepTemplate id="scale" {title} {settings} {...others}>
   <Container label="Type">
   <Select bind:value={type} options={types} />
   </Container>
   {#if type === "als"}
   <Container label="Lambda">
   <Range bind:value={params[1]} min={1} max={7} step={0.1} />
   </Container>
   <Container label="Penalty range">
   <Select bind:value={maxPenalty} options={["1", "0.1", "0.01"]} />
   </Container>
   <Container label="Penalty">
   <Range bind:value={params[2]} min={stepPenalty} max={maxPenaltyNum - stepPenalty} step={stepPenalty} decNum={decNumPenalty}/>
   </Container>
   {/if}
   {#if type === "emsc"}
   <Container label="Polynomial">
   <RangeDiscrete bind:value={params[1]} min={0} max={5} step={1} decNum={0}/>
   </Container>
   {/if}
</PrepTemplate>

