<script>
   /*************************************************************/
   /* Widget for normalization                                  */
   /* -----------------------------------                       */
   /*************************************************************/

   import { Container, Button, ButtonCancel, ButtonUp, ButtonDown } from 'svelte-controls-basic';
	import { fade } from 'svelte/transition';


   let {
      id,
      title,
      settings,
      isOpen,
      pos,
      moveUp,
      moveDown,
      apply,
      remove,
      open,
      cancel = null,
      children
   } = $props();

</script>


{#if isOpen}
<div class="method-open" transition:fade={{duration:10}}>
   <Container><p class="title">{title}</p></Container>
   {@render children()}
   <Container label=" ">
      {#if cancel}<Button text="Cancel" onclick={cancel}/>&nbsp;{/if}
      <Button text="Save" onclick={apply}/>
   </Container>
</div>
{:else}
<Container>
   <!-- svelte-ignore a11y_click_events_have_key_events -->
   <!-- svelte-ignore a11y_no_static_element_interactions -->
   <p>{title} <span title="Edit" onclick={() => open(pos)}>{settings}</span></p>
   <ButtonUp onclick={() => moveUp(pos)}/>
   <ButtonDown onclick={() => moveDown(pos)}/>
   <ButtonCancel onclick={() => remove(pos)}/>
</Container>
{/if}

