<script>
   /*************************************************************/
   /* AppPlots component for training app                       */
   /* -----------------------------------                       */
   /* defines plot components, controls and parameters          */
   /*************************************************************/

   import { Container, Switch, Select } from 'svelte-controls-basic';
   import { Axes, Multilines, XAxis, YAxis, Box, Lines, Rectangles, TextLabels, ColormapLegend, getcolmap} from 'svelte-plots-basic/2d';
   import { text2svg } from 'svelte-plots-basic/utils';
   import { mrange, split } from 'mdatools/stat';
   import { Index } from 'mdatools/arrays';
   import { getObjColors } from '../../../shared/plots/Defaults';
   import { seq } from '../../../shared/utils';

   let {
      rawdata,
      prepdata,
      varind,
      left,
      right,
   } = $props();

   // plotting options
   let show = $state(true);         // if "true" shows plot for preprocessed data, otherwise for raw
   let colorBy = $state('none');    // how to color group lines
   let zoom = $state(false);        // zoom in or not x-axis

   // derived options
   const title = $derived((show ? 'Preprocessed' : 'Raw') + ' data (selected rows)');
   const data = $derived(show && prepdata ? prepdata : rawdata);
   const label = $derived(data ? data.varaxisLabel : 'Variables');
   const units = $derived(rawdata.varaxisUnits);

   // if values for left and right are provided, the values will be trimmed
   // here we check for "show === true" and hence "data" always points to preprocessed data
   // which is a simplified version only with predictors
   const isTrimmed = $derived(left >= 0 || (right > 0 && right < data.values.nrows));
   const indLeft = $derived(left >= 0 ? left + 1: 1);
   const indRight = $derived((right > 0 && right < data.values.nrows) ? right : data.values.nrows);
   const colInd = $derived(show && data && isTrimmed ? Index.seq(indLeft, indRight) : null);
   const xValues = $derived(colInd ? data.varvalues.filter((v, i) => i >= indLeft - 1 && i < indRight) : data.varvalues);
   const yValues = $derived(colInd ? data.values.subset(colInd, null) : data.values);

   // limX is based on original data range not on the trimmed values
   // limY will have extra 5% on top if color legend should be shown
   const yRange = $derived(yValues ? mrange(yValues.v) : null);
   const dY = $derived(yRange ? (yRange[1] - yRange[0]) * 0.05 : null);
   const limY = $derived(yRange ? [yRange[0], yRange[1] + (colorBy !== 'none') * dY] : [0, 1]);
   const xRange = $derived(data ? mrange(data.varvalues, zoom ? 0.005 : 0.1) : [0, 1]);
   const limX = $derived(zoom && isTrimmed? [data.varvalues[indLeft - 1], data.varvalues[indRight - 1]] : xRange)

   // compute vector with colors
   const colors = $derived.by(() => {
      if (!rawdata) return null;

      // one color - in fact will not be used
      const out = {'none': {'colors': '#ff0000'}};

      // color by groups if any
      if (rawdata.classes && rawdata.classNames && rawdata.classNames.length > 1) {
         const c = getcolmap(rawdata.classNames.length);
         out['groups'] = {
            colors: rawdata.classes.map(v => c[rawdata.classNames.findIndex(x => x === v)]),
            breaks: seq(0, rawdata.classNames.length),
            labels: rawdata.classNames,
            map: c
         }
      }

      // color by response if any
      if (rawdata.resp && rawdata.resp.length === rawdata.values.ncols) {
         const breaks = split(rawdata.resp, 8);
         const map = getcolmap(breaks.length - 1);
         out['response'] = {
            colors: getObjColors(rawdata.resp.v, breaks.v, map),
            labels: null,
            breaks,
            map
         }
      }

      return out;
   });

   // effect to reset zoom value
   $effect(() => {
      if (left < 1 || right < 1 || right >= left) {
         zoom = false;
      }
   });
</script>

<div class="plot-panel">
{#if xValues && yValues}
   <!-- column with current plot -->
   <div class="plot-area">
      <div class="plot">
      <Axes {title} {limY} {limX} fileName='plot-preprocessed' >

      <!-- all outcomes -->
      {#if colorBy === 'none'}
         <Multilines {xValues} {yValues} />
      {:else}
         {#each colors[colorBy].colors as col, i}
         <Lines {xValues} yValues={yValues.getcolumn(i + 1)} lineColor={col}/>
         {/each}
      {/if}

      <!-- vertical line for variable based normalization -->
      {#if varind && varind[0] > 0}
      {@const x = data.varvalues[varind[0]]}
      <Lines xValues={[x, x]} yValues={limY} lineColor='#00000060'/>
      <TextLabels
         xValues={[x]}
         yValues={[limY[1] - dY * (colorBy === 'none' ? 1 : 2)]}
         labels={text2svg(x.toFixed(2) + ' ' + units)}
         textColor="#a0a0a0"
         textSize={0.8}
         pos={2}
      />
      {/if}

      <!-- rectangle to show trimming on the left side -->
      {#if varind && varind[1] > 0}
      {@const x = data.varvalues[varind[1]]}
      <Rectangles
         left={[limX[0]]}
         width={[data.varvalues[varind[1]] - limX[0]]}
         top={[limY[1]]}
         height={[limY[1] - limY[0]]}
         lineColor="transparent"
         faceColor="#00000040"
      />
      <TextLabels
         xValues={[x]}
         yValues={[limY[1] - dY * (colorBy === 'none' ? 1 : 2)]}
         labels={text2svg(x.toFixed(2) + ' ' + units)}
         textColor="#a0a0a0"
         textSize={0.8}
         pos={2}
      />
      {/if}

      <!-- rectangle to show trimming on the right side -->
      {#if varind && varind[2] < data.varvalues.length && varind[2] > 0}
      {@const x = data.varvalues[varind[2] - 1]}
      <Rectangles
         left={[data.varvalues[varind[2] - 1]]}
         width={[limX[1] - data.varvalues[varind[2] - 1]]}
         top={[limY[1]]}
         height={[limY[1] - limY[0]]}
         lineColor="transparent"
         faceColor="#00000040"
      />
      <TextLabels
         xValues={[x]}
         yValues={[limY[1] - dY * (colorBy === 'none' ? 1 : 2)]}
         labels={text2svg(x.toFixed(2) + ' ' + units)}
         textColor="#a0a0a0"
         textSize={0.8}
         pos={2}
      />
      {/if}

      <ColormapLegend show={colorBy !== 'none'} labels={colors[colorBy].labels}  breaks={colors[colorBy].breaks} colmap={colors[colorBy].map} />
      <XAxis label={label} showGrid={true} />
      <YAxis showGrid={true} />
      <Box />
   </Axes>
      </div>
   </div>

   <!-- column with plot selectors and current plot settings -->
   <div class="plot-controls-area">
      <Container label=""><h2>Plot settings</h2></Container>
      <Container label="Show">
         <Switch bind:value={show} options={["raw", "preprocessed"]} />
      </Container>
      {#if Object.keys(colors).length > 1}
      <Container label="Color by">
         <Select bind:value={colorBy} options={Object.keys(colors)} />
      </Container>
      {/if}
      {#if (left > 0 && right > left)}
      <Container label="Zoom (x-axis)">
         <Switch bind:value={zoom} options={["off", "on"]} />
      </Container>
      {/if}
   </div>
{/if}
</div>

<style>

   .plot-panel {
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      padding: 0;
      display: grid;
      grid-template-rows: 1fr;
      grid-template-columns: 1fr minmax(270px, 30%);
   }

   .plot-area {
      padding: 1em;
      margin-top: 0.25rem;
      border-radius: 0.25em;
      background: #fefefe;
   }

   .plot-controls-area {
      padding: 0 0.25em 0 0.5em;
   }

   :global(div.plot-container.off-screen) {
      position:absolute;
      top:0;
      left:50%;
      width: 650px;
      height: 600px;
      background: white;
      z-index: 999;
   }

   .plot {
      width: 100%;
      height: calc(100% - 15px);
   }


   @media(min-width:1100px) {
      .plot-panel {
         grid-template-columns: 1fr minmax(285px, 30%);
      }
   }

   @media(min-width:1300px) {
      .plot-panel {
         grid-template-columns: 1fr max(300px, 30%);
      }
   }

   @media(min-width:1600px) {
      .plot-panel {
         grid-template-columns: 1fr 400px;
      }
   }

</style>