import { Vector, Matrix, Index } from 'mdatools/arrays';
import { downloadData, downloadJSON, excludeVars, seq } from '../../shared/utils.js';


/**
 * Returns truncated (short) dataset for quick demonstration of preprocessing effect.
 * @param {Object} d - JSON with full data.
 * @param {number} n - number of rows to subset.
 */
export function getShortData(d, n) {
   // get response values if any
   let resp = 0;
   let offset = 0;
   if (d.nresp > 0) {
      resp = new Vector(d.values.v.slice(0, d.nresp * d.values.nrows));
      offset = d.nresp;
   }

   // get predictor values and transpose  them so objects are columns
   const v = (new Matrix(d.values.v.slice(offset * d.values.nrows), d.values.nrows, d.values.ncols - offset)).t();

   const step = (v.ncols < n ? 1 : Math.round(v.ncols / n));
   const colInd = Index.seq(1, v.ncols, step);
   const valuesShort = v.subset(null, colInd);
   const respShort = resp ? resp.subset(colInd) : null;
   const classesShort = d.classes ? d.classes.filter((v, i) => colInd.v.includes(i)) : null;

   return {
      classNames: d.classNames,
      classes: classesShort,
      resp: respShort,
      values: valuesShort,
      varvalues: d.varvalues.filter((v, i) => i >= offset),
      varindices: d.varindices.filter((v, i) => i >= offset),
      varaxisLabel: d.varvaluesUnits ? d.varvaluesName + ' ' + d.varvaluesUnits : d.varvaluesName,
      varaxisUnits: d.varvaluesUnits
   }
}


/**
 * Creates a copy (clone) of short data.
 * @param {Object} d - JSON with short data
 * @param {Matrix} values - matrix with new (e.g. preprocessed) values to replace (if undefined will be taken from d.values).
 */
export function cloneShortData(d, values) {
   return {
      values: values ? values : d.values.copy(),
      varvalues: d.varvalues,
      varindices: d.varindices,
      varaxisLabel: d.varaxisLabel,
   }
}


/**
 * Creates a copy (clone) of full data.
 * @param {Object} d - JSON with full data
 * @param {Matrix} values - matrix with new (e.g. preprocessed) values to replace (if undefined will be taken from d.values).
 */
export function cloneFullData(d, values) {
   if (!values || values.nrows !== d.values.nrows || values.ncols !== d.values.ncols) {
      throw new Error('Object with values seems to be wrong for cloning.');
   }

   const out = structuredClone(d);
   out.values = values;
   return out;
}


/**
 * Truncate and download preprocessed data.
 * @param {Object} prepdata - JSON with preprocessed data.
 * @param {Number} left - index of left column to keep in the data.
 * @param {Number} right - index of right column to keep in the data.
 */
export function downloadPrepData(prepdata, left , right) {
   const filename = prepdata.fileName + "-preprocessed";
   downloadData(prepdata, filename);
}


/**
 * Download preprocessing model as JSON file.
 * @param {Object} prepmodel - JSON with model values.
 */
export function downloadPrepModel(prepmodel) {
   const fileName = 'preprocessing-model';
   downloadJSON(prepmodel, fileName);
}