<script>
   /*************************************************************/
   /* Widget for Savitzky-Golay                                 */
   /* -----------------------------------                       */
   /*************************************************************/

   import { Container, Select, Range, RangeDiscrete } from 'svelte-controls-basic';
   import PrepTemplate from './PrepTemplate.svelte';


   let {
      params = $bindable([3, 1, 0]),
      settings = $bindable(),
      nvars,
      ...others
   } = $props();


   // compute max filter width
   const maxWidth = $derived.by(() => {
      if (!nvars) return 3;
      const v = Math.round(nvars * 0.25);
      if (v > 39) return 39;
      return v % 2 < 1 ? v + 1 : v;
   });

   // derive options for derivative and polynomial
   const derOptions = $derived(params[1] < 2 ? [0, 1] : [0, 1, 2]);
   const polOptions = $derived(params[0] < 5 ? [1, 2] : [1, 2, 3]);

   // adjust polynomial if filter width is too small
   $effect(() => {
      if (params[0] < 5 && params[1] > 2) params[1] = 2;
   });

   // adjust derivative if polynomial is too small
   $effect(() => {
      if (params[2] > params[1]) params[2] = params[1];
   });

   $effect(() => {
      settings = params[0] + ', ' + params[1] + ', ' + params[2];
   });

   const title = 'Savitzkiy-Golay';

</script>


<PrepTemplate id="savgol" {title} {settings} {...others}>
   <Container label="Width">
   <RangeDiscrete bind:value={params[0]} min={3} max={maxWidth} step={2}/>
   </Container>
   <Container label="Polynomial">
   <Select bind:value={params[1]} options={polOptions} />
   </Container>
   <Container label="Derivative">
   <Select bind:value={params[2]} options={derOptions} />
   </Container>
</PrepTemplate>

