<script>
   /*************************************************************/
   /* PrepCreator                                               */
   /* ------------                                              */
   /* creates preprocessing object with directives and user     */
   /* defined settings and apply it to short data               */
   /*************************************************************/

   import { Container, ButtonAdd, ButtonSettings, Button } from 'svelte-controls-basic';
   import { prep_fit, prep_apply, methodsList } from '../../../shared/wasm/dist/prep.js';
   import { cloneShortData, getShortData } from '../utils.js';

   import PrepNorm from '../widgets/PrepNorm.svelte';
   import PrepSavgol from '../widgets/PrepSavgol.svelte';
   import PrepScale from '../widgets/PrepScale.svelte';
   import PrepBaseline from '../widgets/PrepBaseline.svelte';
   import PrepTrim from '../widgets/PrepTrim.svelte';
   import PrepSpikes from '../widgets/PrepSpikes.svelte';

   let {
      WASM,                            // WASM module
      rawdata,                         // original data loaded by user
      prepobj = $bindable([]),         // JSON with preprocessing object
      meta = $bindable(),              // Array with meta information (text settings values)
      varind = $bindable(-1),          // variable index for normalization (if in use)
      rawdataShort = $bindable(),
      prepdataShort = $bindable(),
      prepmodel = null,                // just to trace if model is available or not use it only in prep app
      left,
      right,
      setLeft,
      setRight,
      reset = null,
      finalize = null,
   } = $props();

   // constant parameters
   const NROWS = 30;                // how many rows to take to the short data
   const MAX_NMETHODS = 8;          // maximum number of methods in preprocessing chain
   const methodsComponents = {      // list of availabl methods components
      'savgol' : PrepSavgol,
      'norm': PrepNorm,
      'scale': PrepScale,
      'baseline': PrepBaseline,
      'trim': PrepTrim,
      'spikes': PrepSpikes,
   };

   // error handling parameters and promis for WASM actions
   let status = $state('neutral');
   let errMsg = $state('');
   let promise = $state();

   // state variables for business logic
   let openMethod = $state('');         // position of a method that is currently open (settings panel)
   let methods = $state([]);            // array of currently selected methods
   let showCancel = $state(false);


   // state variables to keep left and right values for each item in perpobj
   let leftVec = $state([]);
   let rightVec = $state([]);

   // prepare meta as empty array
   meta = new Array(MAX_NMETHODS).fill('');

   // derived for keeping nvars
   const nvars = $derived(rawdata.values.ncols - rawdata.nresp);

   // local copy of the available methods list
   let methodsListLocal = $state(
      rawdata.values.nrows < 3 ?
      Object.keys(methodsComponents).filter(v => v !== 'scale') :
      Object.keys(methodsComponents)
   );

   // when component is being reloaded make sure trimming is correct
   trimTails();

   // if prepobject is not empty (e.g. user edit already created preprocessing routine)
   // apply it to short data
   if (prepobj.length !== 0) prepApplyShort();


   /****************************/
   /* Helper functions         */
   /****************************/

   // applies current preprocessing sequence to short dataset.
   async function prepApplyShort() {

      if (prepobj.length === 0) return;
      promise = WASM()
         .then(Module => {
            status = 'applying';

            // fit model and add extra parameters
            const m = prep_fit(Module, rawdataShort.values, 0, prepobj, true, []);
            const v = prep_apply(Module, rawdataShort.values, 0, m, true);

            // set global variables and status of the component
            prepdataShort = cloneShortData(rawdataShort, v);
            status = 'success';
         })
         .catch(e =>{
            // something went wrong
            prepdataShort = cloneShortData(rawdataShort);
            console.error(e);
            status = 'error';
            errMsg = e;
         });
   }

   // Checks "prepobj" elements if it contains trimming, adjust left and right values accordingly.
   function trimTails() {
      const npred = rawdata.values.ncols - rawdata.nresp;
      if (!rawdata) return;
      if (!prepobj || prepobj.length < 1) {
         setLeft(0);
         setRight(npred);
         leftVec = [];
         rightVec = [];
         return
      }

      let lv = [];
      let rv = [];
      let l = 0;
      let r = npred;
      for (let v of prepobj) {
         if (v.id === 'trim') {
            lv.push(l);
            rv.push(r);
            // correct params if they are beyound the borders (e.g. after moving)
            if (v.params[0] < l) v.params[0] = l;
            if (v.params[1] > r) v.params[1] = r;
            l = v.params[0] > l ? v.params[0] : l;
            r = v.params[1] > 0 && v.params[1] < r ? v.params[1] : r;
         } else {
            lv.push(l);
            rv.push(r);
         }
      }
      leftVec = lv;
      rightVec = rv;
      setLeft(l);
      setRight(r);
   }

   // adds method with provided "id" to selected list.
   function add(id) {
      // locate the method by id
      const method = methodsList.find(v => v.id === id);
      if (!method) return

      // add method and default parameters to preprocessing object and remove it from the available methods list
      leftVec.push(left);
      rightVec.push(right);
      prepobj.push({id: method.id, params: method.mp});

      // open the settings dialog
      showCancel = true;
      openMethod = prepobj.length - 1;
   }

   // cancels adding the last method.
   function cancel() {

      if (prepobj.length < 1) return;

      leftVec.pop();
      rightVec.pop();
      prepobj.pop();
      showCancel = false;
      openMethod = -1;
   }


   // moves method at position "pos" one step up
   function moveUp(pos) {

      if (pos <= 0) return;

      // swap the method with the one above
      const method = prepobj[pos];
      const prev = prepobj[pos - 1];
      prepobj[pos - 1] = method;
      prepobj[pos] = prev;
      apply();
   }

   // moves method at position "pos" one step down
   function moveDown(pos) {

      // find position of the method in the list
      if (pos < 0 || pos === prepobj.length - 1) return;

      // swap the method with the one below
      const method = prepobj[pos];
      const next = prepobj[pos + 1];
      prepobj[pos + 1] = method;
      prepobj[pos] = next;
      apply();
   }

   // removes method at position "pos" from the selected list.
   function remove(pos) {

      // find the method in the list
      if (pos < 0 || pos >= prepobj.length) return;

      // remove the method from the lis of selected and add it back to the list of available methods
      prepobj = prepobj.filter((v, i) => i !== pos);
      apply();
   }

   // opens settings dialog for method at position "pos" in the method list
   function open(pos) {
      openMethod = pos;
   }

   // closes dialog without applying new settings
   function close() {
      openMethod = null;
   }

   // applies preprocessing to the short dataset.
   function apply() {
      status = 'neutral'
      errMsg = '';
      showCancel = false;
      openMethod = null;

      // if model has been created (user clicked finalize) reset if
      if (prepmodel) reset();

      // preprocessing chain is about to change - reset model and preprocessed data
      trimTails();
      prepApplyShort();
      varind[1] = -1;
      varind[2] = -1;
   }


   /****************************/
   /* Effects                  */
   /****************************/

   // effect to generate short dataset when necessary
   $effect(() => {
      if (rawdata && !rawdataShort) {
         rawdataShort = getShortData(rawdata, NROWS);
      }
   })

   // effect to clone short dataset into preprocessed short
   $effect(() => {
      if (rawdataShort && !prepdataShort) {
         prepdataShort = cloneShortData(rawdataShort);
      }
   })

   // effect to reset all component settings if there are no methods in preprocessing object
   $effect(() => {
      if (!prepobj || prepobj.length === 0) {
         methods = [];
         prepdataShort = null;
         leftVec = [];
         rightVec = [];
         openMethod = '';
         meta = new Array(MAX_NMETHODS).fill('');
      }
   });
</script>

<div class="selected-methods-list"  class:final={prepmodel !== null && prepmodel !== undefined}>

   <!-- block header with edit button -->
   <Container label="">
      <h2>Selected methods</h2>
      {#if prepmodel && reset}
      <ButtonSettings text="edit" onclick={reset}/>
      {/if}
   </Container>

   {#if prepobj.length === 0}
      <Container label=""><p>No methods selected</p></Container>
   {:else}
      {#each prepobj as m, i}
         {@const Component = methodsComponents[m.id]}
         {#if m.id === 'norm' || m.id === 'trim'}
         <Component
            bind:params={m.params}
            bind:varind={varind}
            bind:settings={meta[i]}
            isOpen={i === openMethod}
            left={leftVec[i]}
            right={rightVec[i]}
            pos={i}
            {nvars}
            {moveUp} {moveDown} {apply} {open} {close} {remove} cancel={showCancel ? cancel : null}
         />
         {:else}
         <Component
            bind:params={m.params}
            bind:settings={meta[i]}
            pos={i}
            isOpen={i === openMethod}
            left={leftVec[i]}
            right={rightVec[i]}
            {nvars}
            {moveUp} {moveDown} {apply} {open} {close} {remove} cancel={showCancel ? cancel : null}
         />
         {/if}
      {/each}
   {/if}


   <!-- error message -->
   {#if status === 'error' || errMsg}
   <Container status="error">
      <p>{errMsg}</p>
   </Container>
   {/if}

</div>

<div class="downloads-block">
   {#if !prepmodel && !openMethod && prepobj.length > 0}
      <Container label=" ">
         <Button onclick={finalize} text="Finalize" />
      </Container>
   {/if}
</div>

<div class="methods-list">
   <Container label=""><h2>Available methods</h2></Container>
   {#if prepobj.length >= MAX_NMETHODS}
      <Container label=""><p>Maximum number of methods is selected.</p></Container>
   {:else}
      {#each methodsListLocal as id}
      {@const m = methodsList.find(v => v.id === id)}
      <Container>
         <p>{m.title}</p>
         <ButtonAdd onclick={() => add(m.id)} />
      </Container>
      {/each}
   {/if}
</div>




<style>
   .selected-methods-list {
      background: #fefafaa0;
      border-radius: 5px;
   }

   .selected-methods-list > :global(.control-element:last-child .button-down),
   .selected-methods-list > :global(.control-element:nth-child(2) .button-up) {
      cursor:text;
      background-color: transparent;
      color: transparent;
   }


   .selected-methods-list:not(.editable) > :global(.control-element .button-settings) {
      display: block;
   }

   .methods-list {
      margin-top: 1em;
      font-size: 0.9em;
   }

   .methods-list > :global(div) {
      background: #ebf0f0;
   }

   .final :global(.button-up),
   .final :global(.button-down),
   .final :global(.button-cancel) {
      visibility: hidden;
   }

</style>