<script>
   /*************************************************************/
   /* Widget for normalization                                  */
   /* -----------------------------------                       */
   /*************************************************************/

   import { Container, Select, RangeDiscrete, Widget, Button } from 'svelte-controls-basic';
   import PrepTemplate from './PrepTemplate.svelte';

   let {
      params = $bindable([0, 0]),
      varind = $bindable(),
      settings = $bindable(),
      isOpen,
      nvars = 0,
      ...others
   } = $props();

   // type to num maps
   const typesMap = {'snv': 0, 'area': 1, 'length': 2, 'var': 3};
   const types = Object.keys(typesMap);

   // initial settings
   let type = $state(types[params[0]]);
   let varPos = $state(params[1] + 1);

   const title = 'Normalization';

   $effect(() => {
      settings = type + (type === 'var' ? ' #' + varPos : '');
   });

   // effect to trace var based normalization
   $effect(() => {
      if (type === 'var') {
         varind[0] = params[1] > 0 ? params[1] : 1;
      } else {
         varind[0] = -1;
      }
   });

   // effect for backtrace
   $effect(() => {
      if (type === 'var' && isOpen) {
         varind[0] = params[1];
      } else {
         varind[0] = -1;
      }
   });

   // effect to trace type changes
   $effect(() => {
      if (type) params[0] = typesMap[type];
   });

   $effect(() => {
      if (varPos) params[1] = varPos - 1;
   })
</script>


<PrepTemplate id="norm" {title} {settings} {isOpen} {...others}>
   <Container label="Type">
   <Select bind:value={type} options={types} />
   </Container>
   {#if type === 'var'}
   <Container label="Variable index">
   <RangeDiscrete bind:value={varPos} min={1} max={nvars} step={1} />
   </Container>
   {/if}
</PrepTemplate>

