import { mean, variance, median, sd, iqr } from 'mdatools/stat';
import { ismatrix, Vector } from 'mdatools/arrays';

import { qchisq } from 'mdatools/distributions';
import { scale as prep_scale} from 'mdatools/prep';

import { genhash, seq, replacer, convertToIntervals } from '../../shared/utils.js';
import { processMembers, processStrangers } from '../../ddsimca/src/utils.js';




/**
 * Download CSV file with NIMCA results.
 *
 * @param {Object} nimca - JSON with NIMCA model.
 * @param {Object} res - JSON with results object.
 * @param {Object} dataset - JSON with original dataset.
 * @param {string} name - name of the results ('cal', 'test', 'pv', 'new')
 * @param {Array} outliers - array with excluded objects
 */
export function downloadResults(nimca, res, dataset, name, outliers) {

   if (!outliers || !Array.isArray(outliers)) {
      outliers = [];
   }

   let exclvars = nimca.exclvars;
   if (!exclvars || !Array.isArray(exclvars)) {
      exclvars = [];
   }

   const nAll = dataset.values.nrows;
   const nOut = outliers.length;
   const n = nAll - nOut;
   const sep = dataset.sep;

   const hasClasses = res.classes && res.classes.length === n;
   const filler = hasClasses ? ['', '', '', '', ''] : ['', '', '', ''];
   let out = '';
   out += ['Reference classes:',  hasClasses ? 'provided' : 'not provided', ...filler].join(sep) + '\n';
   out += ['Center:', (nimca.center ? 'yes' : 'no'), ...filler].join(sep) + '\n';
   out += ['Scale:', (nimca.scale ? 'yes' : 'no'), ...filler].join(sep) + '\n';
   out += ['Alpha:', (nimca.alpha/100).toFixed(3), ...filler].join(sep) + '\n';
   out += ['Limits estimator:', nimca.limType, ...filler].join(sep) + '\n';
   out += '\n';

   out += ['Nf:', res.Nf, ...filler].join(sep) + '\n';
   out += '\n';

   out += ['F crit (extremes):', nimca.eCrit, ...filler].join(sep) + '\n';
   out += ['F crit (outliers):', nimca.oCrit, ...filler].join(sep) + '\n';
   out += '\n';

   if (exclvars.length > 0) {
      out += ['Number of excluded variables:', exclvars.length, ...filler].join(sep) + '\n';
      out += ['Indices of excluded variables:', convertToIntervals(exclvars), ...filler].join(sep) + '\n';
      out += '\n';
   }

   // overall statistics
   out += ['Objects:', nAll, ...filler].join(sep) + '\n';
   out += ['In:', res.outcomes.decsNum.in, ...filler].join(sep) + '\n';
   out += ['Out:', res.outcomes.decsNum.out, ...filler].join(sep) + '\n';
   out += ['Excluded:', nOut, ...filler].join(sep) + '\n';

   // statistics for members if any
   if (res.nMembers > 0) {
      out += '\n';
      out += ['Members:', res.nMembers, ...filler].join(sep) + '\n';
      out += ['Regular:', res.outcomes.rolsNum.regular, ...filler].join(sep) + '\n';
      out += ['Extreme:', res.outcomes.rolsNum.extreme, ...filler].join(sep) + '\n';
      out += ['Outliers:', res.outcomes.rolsNum.outlier, ...filler].join(sep) + '\n';
      out += ['Sensitivity:', res.outcomes.stat.sensitivity, ...filler].join(sep) + '\n';
   }

   // statistics for strangers if any
   if (res.nStrangers > 0) {
      out += '\n';
      out += ['Strangers:', res.nStrangers, ...filler].join(sep) + '\n';
      out += ['In:', res.outcomes.decsNum.in - res.outcomes.rolsNum.regular, ...filler].join(sep) + '\n';
      out += ['Aliens:', res.outcomes.rolsNum.alien, ...filler].join(sep) + '\n';
      out += ['External:', res.outcomes.rolsNum.external, ...filler].join(sep) + '\n';
      out += ['Specificity:', res.outcomes.stat.specificity, ...filler].join(sep) + '\n';
      out += ['Selectivity:', res.outcomes.stat.selectivity, ...filler].join(sep) + '\n';

      out += '\n';
      out += ['Beta:', res.beta, ...filler].join(sep) + '\n';
      out += ['Non-centrality (s):', res.s, ...filler].join(sep) + '\n';
      out += ['Scaling (f0):', res.f0, ...filler].join(sep) + '\n';

      if (res.nMembers > 0) {
         const PPV = res.outcomes.TP / (res.outcomes.TP + res.outcomes.FP)
         const TPR = res.outcomes.stat.sensitivity;
         const FPR = 1 - res.outcomes.stat.specificity;
         const F1 = 2 * PPV * TPR / (PPV + TPR)
         const PLR = TPR / FPR;
         const NLR = (1 - TPR) / (1 - FPR);
         out += '\n';
         out += ['Efficiency:', res.outcomes.stat.efficiency, ...filler].join(sep) + '\n';
         out += ['Accuracy:', res.outcomes.stat.accuracy, ...filler].join(sep) + '\n';
         out += ['Precision:', PPV, ...filler].join(sep) + '\n';
         out += ['F1-score:', F1, ...filler].join(sep) + '\n';
         out += ['PLR:', PLR, ...filler].join(sep) + '\n';
         out += ['NLR:', NLR, ...filler].join(sep) + '\n';
      }
   }


   // outcomes for every object
   out += '\n';
   out += ['', 'object', 'class', 'role', 'decision', 'f'].join(sep) + '\n';


   let j = 0;
   for (let i = 0; i < nAll; i++) {

      if (nOut > 0 && outliers.includes(i)) {
         out += [
            '',
            dataset.objlabels[i],
            '-',
            'removed',
            '-',
            '-'
         ].join(sep) + '\n';
         continue;
      }

      out += [
         '',
         dataset.objlabels[i],
         res.classes[j],
         res.outcomes.rols[j],
         res.outcomes.decs[j],
         res.outcomes.f[j]
      ].join(sep) + '\n';
      j = j + 1;
   }
   out += '\n';



   if (dataset.dec !== '.') {
      out = out.replace(/\./g, dataset.dec);
   }

   out = ['Target class:', nimca.className, ...filler].join(sep) + '\n'  + out;
   out = ['Data filename:', dataset.fileName, ...filler].join(sep) + '\n' + out;

   let hdr = ['NIMCA results (' + name + ')', 'https://mda.tools/nimca/', ...filler].join(sep) + '\n\n';

   const blob = new Blob([hdr + out], { type: 'text/csv' });
   const url = window.URL.createObjectURL(blob)
   const a = document.createElement('a')
   a.setAttribute('href', url)
   a.setAttribute('download', `nimca-results-${name}.csv`);
   a.click()
}


/**
 * Download DDSIMCA model as JSON file.
 *
 * @param {Object} nimca - JSON with NIMCA model.
 * @param {Object} prepmodel - JSON with preprocessing model.
 */
export function downloadModel(nimca, prep) {

   const out = {
      'nimca': nimca.asJSON(),
      'prep': prep
   }
   const blob = new Blob([JSON.stringify(out, replacer)], { type: 'application/json' });
   const url = window.URL.createObjectURL(blob)
   const a = document.createElement('a')
   a.setAttribute('href', url)
   a.setAttribute('download', 'nimca-model.json');
   a.click()
}


/**
 * Class for Naive SIMCA results
 */
export class NIMCARes {

   /**
    * Constructor
    * @param {Vector} d - vector with distance values
    * @param {} ind
    * @param {*} objlabels
    * @param {*} objindices
    * @param {*} classes
    * @param {*} classNames
    * @param {*} m
    */
   constructor(d, ind, objlabels, objindices, classes, classNames, m) {
      this.d = d;
      this.ind = ind;
      this.objlabels = objlabels;
      this.objindices = objindices;
      this.classes = classes;
      this.classNames = classNames;
      this.nrows = d.length;
      this.nMembers = ind.members.length;
      this.nStrangers = ind.strangers.length;
      this.nUnknown = ind.unknown.length;
      this.outcomes = $derived(m.oCrit && m.eCrit ? this.getOutcomes(m) : null);
   }

   getOutcomes(model) {

      const f = this.d.apply(v => v / model.f0 * model.Nf).v;
      const decs = new Array(this.nrows);
      const rols = new Array(this.nrows);
      const rolsNum = {'regular': 0, 'extreme': 0, 'outlier': 0, 'alien': 0, 'external': 0};
      const decsNum = {'in': 0, 'out': 0};

      const [TP, FN] = processMembers(f, model.eCrit, model.oCrit, rols, decs, rolsNum, decsNum, this.ind.members);
      const [TN, FP, beta, s, f0, hz, Mz, Sz, k, m] = processStrangers(f, model.Nf, model.eCrit, model.oCrit, rols, decs, rolsNum, decsNum, this.ind.strangers);

      if (this.nUnknown > 0) {
         const tmp = processStrangers(f, model.Nf, model.eCrit, model.oCrit, rols, decs, rolsNum, decsNum, this.ind.unknown);
      }

      const sensitivity = TP > 0 ? TP / (TP + FN) : 0;
      const specificity = TN > 0 ? TN / (TN + FP) : 0;
      const selectivity = 1 - beta;
      const efficiency = Math.sqrt(sensitivity * specificity);
      const accuracy = TP > 0 && TN > 0 ? (TP + TN) / (TP + TN + FP + FN) : 0;

      const stat = {sensitivity, specificity, selectivity, efficiency, accuracy};
      return {Nf: model.Nf, f, decs, rols, decsNum, rolsNum, TP, FN, TN, FP, beta, s, f0, hz, Mz, Sz, k, m, stat}
   }

   toJSON() {
      return null;
   }


   static fromJSON(obj) {
      return new NIMCARes(...obj);
   }
}


/**
 * Class for NIMCA model
 */
export class NIMCA {

   /**
    * Class constructor
    * @param {string} className - name of the target class.
    * @param {Vector} mX - vector with values for centering.
    * @param {Vector} sX - vector with values for scaling.
    * @param {number} nrows - number of rows in the training set.
    * @param {number} ncols - nuimber of columns (predictors).
    * @param {number} Nfc - number of degrees of freedom computed using classic approach.
    * @param {number} Nfr - number of degrees of freedom computed using robust approach.
    * @param {number} f0c - scalar computed using classic approach.
    * @param {number} f0r - scalar computed using robust approach.
    * @param {string} hash - unique ID (hash).
    * @param {number} alpha - (state) significance level for classification.
    * @param {number} gamma - (state) significance level for outliers detection.
    * @param {string} limType - (state) limit type for current model.
    */
   constructor(className, mX, sX, nrows, ncols, Nfc, Nfr, f0c, f0r, hash, alpha, gamma, limType, center, scale, fileName = 'nimca-model') {

      // constant properties
      this.mX = mX;
      this.sX = sX;
      this.nrows = nrows;
      this.ncols = ncols;
      this.className = className;
      this.Nfc = Nfc;
      this.Nfr = Nfr;
      this.f0c = f0c;
      this.f0r = f0r;
      this.hash = hash;
      this.class = ['nimcamodel'];
      this.fileName = fileName;
      this.center = center;
      this.scale = scale;

      // state properties
      this.alpha = $state(alpha);
      this.gamma = $state(gamma);
      this.limType = $state(limType);

      // derived properties
      this.Nf = $derived(this.limType === 'classic' ? this.Nfc : this.Nfr);
      this.f0 = $derived(this.limType === 'classic' ? this.f0c : this.f0r);
      this.eConfLim = $derived(1 - Number.parseFloat(this.alpha) / 100);
      this.oConfLim = $derived(Math.pow(1 - this.gamma / 100, 1 / nrows));
      this.eCrit = $derived(qchisq(this.eConfLim, this.Nf));
      this.oCrit = $derived(qchisq(this.oConfLim, this.Nf));
   }

   /**
    * Applies NIMCA model to new dataset.
    * @param {Object} dataset - JSON with dataset.
    * @param {Array} outliers - array with outliers.
    * @returns {NIMCARes} result object.
    */
   predict(dataset) {

      if (!dataset.values || !ismatrix(dataset.values)) {
         throw new Error("nimca.predict: 'dataset' does not contain values as Matrix object.");
      }

      if (dataset.values.ncols != this.ncols) {
         throw new Error("nimca.predict: 'dataset' values have wrong number of columns (expect: " + this.ncols + ").");
      }

      const d = NIMCA.getDistance(dataset.values, this.mX, this.sX);
      const ind = {members: [], strangers: [], unknown: []};
      if (dataset.classes && dataset.classes.length === dataset.values.nrows) {
         for (let i = 0; i < dataset.classes.length; i++) {
            if (dataset.classes[i] === this.className) {
               ind.members.push(i);
            } else {
               ind.strangers.push(i);
            }
         }
      } else {
         ind.unknown = seq(0, dataset.values.nrows - 1);
      }
      return new NIMCARes(d, ind, dataset.objlabels, dataset.objindices, dataset.classes, dataset.classNames, this);
   }


   /**
    * Converts model object to pure JSON.
    * @returns {Object}
    */
   asJSON() {
      return {
         __type: 'NIMCA',
         class: this.class,
         fileName: this.fileName,
         mX: this.mX,
         sX: this.sX,
         nrows: this.nrows,
         ncols: this.ncols,
         className: this.className,
         Nfc: this.Nfc,
         Nfr: this.Nfr,
         f0c: this.f0c,
         f0r: this.f0r,
         hash: this.hash,
         center: this.center,
         scale: this.scale,
         alpha: this.alpha,
         gamma: this.gamma,
         limType: this.limType
      };
   }

   /**
    * Creates a model based on the training set.
    * @param {Object} calset - training set.
    * @param {Array} outliers - outliers.
    * @param {Object} params - initial state parameters for the model.
    * @returns {NIMCA} object with model.
    */
   static fit(calset, center, scale, params = {alpha: 5, gamma: 1, limType: 'classic'}) {

      if (!calset.values) {
         throw new Error("nimca.fit: parameter 'calset' does not contain values as Matrix object.");
      }

      if (!calset.classNames || calset.classNames.length != 1) {
         throw new Error("nimca.fit: parameter 'calset' should contain class labels for a target class only.");
      }

      let X = calset.values;
      const className = calset.classNames[0];

      if (!ismatrix(X)) {
         throw new Error("nimca.fit: X must be a matrix.");
      }

      // here X is already without outliers, so we do not provide them to getDistance() method
      const mX = center ? X.apply(mean, 2) : Vector.zeros(X.ncols);
      const sX = scale ? X.apply(sd, 2) : Vector.ones(X.ncols);
      const d = NIMCA.getDistance(X, mX, sX);

      // classic estimates
      const f0c = mean(d);
      const s2f = variance(d);
      let Nfc = Math.round(2 * f0c * f0c / s2f);
      if (Nfc < 1) Nfc = 1;
      if (Nfc > 250) Nfc = 250;


   // Mu <- apply(U, 2, median)
   // Su <- apply(U, 2, IQR)

   // RM <- Su / Mu
   // Nu <- round(exp((1.380948 * log(2.68631 / RM)) ^ 1.185785))
   // Nu[RM > 2.685592117] <- 1
   // Nu[RM < 0.194565995] <- 100

   // u0 <- 0.5 * Nu * (Mu / qchisq(0.50, Nu) + Su / (qchisq(0.75, Nu) - qchisq(0.25, Nu)))
   // return(list(u0 = u0, Nu = Nu, nobj = nrow(U)))

      const Mf = median(d);
      const Sf = iqr(d);
      const RM = Sf / Mf;

      let Nfr;
      if (RM > 2.685592117) {
         Nfr = 1;
      } else if (RM < 0.194565995) {
         Nfr = 100;
      } else {
         Nfr = Math.round(Math.exp(Math.pow(1.380948 * Math.log(2.68631 / RM), 1.185785)));
      }

      // robust estimate for f0
      const f0r = 0.5 * Nfr * (Mf / qchisq(0.50, Nfr) + Sf / (qchisq(0.75, Nfr) - qchisq(0.25, Nfr)));

      return new NIMCA(className, mX, sX, X.nrows, X.ncols, Nfc, Nfr, f0c, f0r, genhash(),
         params.alpha, params.gamma, params.limType, center, scale);
   }


   /**
    * Creates NIMCA object from pure JSON representation.
    * @param {Object} obj - JSON with model parameters.
    * @returns {NIMCA} object with model.
    */
   static fromJSON(obj) {
      const {__type, mX, sX, nrows, ncols, className, Nfc, Nfr, f0c, f0r, hash, alpha, gamma, limType, fileName, center, scale} = obj;
      return new NIMCA(className, mX, sX, nrows, ncols, Nfc, Nfr, f0c, f0r, hash, alpha, gamma, limType, center, scale, fileName);
   }

   /**
    * Computes full distance for new data values.
    * @param {Matrix} X - matrix with data values.
    * @param {Vector} mX - vector with values for centering.
    * @param {Vector} sX - vector with values for scaling.
    * @returns {Vector} vector with distances.
    */
   static getDistance(X, mX, sX) {
      const Xp = prep_scale(X, mX, sX);
      const nrows = Xp.nrows;
      const ncols = Xp.ncols;
      const d = Vector.zeros(nrows);
      for (let c = 0; c < ncols; c++) {
         const xc = Xp.getcolref(c + 1);
         for (let r = 0; r < nrows; r++) {
            d.v[r] += xc[r] ** 2;
         }
      }
      return d;
   }
}
