import { Matrix, Index, isvector, vector, matrix, ismatrix } from 'mdatools/arrays';
import { mean, sd } from 'mdatools/stat';
import { qf } from 'mdatools/distributions';

import { pca_predict, pcamodel_get } from './wasm/dist/pca.js';
import { pls_predict } from './wasm/dist/pls.js';
import { gls_predict } from './wasm/dist/gls.js';
import { prep_apply } from './wasm/dist/prep.js';

import { NIMCA } from '../nimca/src/utils.svelte.js';


// constant values (colors, etc)
export const LINE_COLORS = {'cal': '#2679B2', 'pv': '#D22C2F', 'target': '#2679B2', 'alt': '#2679B2'};
export const FACE_COLORS = {'cal': '#f0f8ff', 'pv': '#fff8f0', 'target': '#f0f8ff', 'alt': '#f0f8ff'};
export const LABELS_COLOR = '#a0a0a0';
export const LABELS_COLOR_SELECTED = '#909090';


/**
 * Creates array with a sequence of values from `start` to `end`.
 * @param {number} start first value of sequence.
 * @param {number} end last value of sequence.
 * @param {number} by increment.
 * @returns {Array} array with sequence.
 */
export function seq(start, end, by = 1) {
   const length = Math.round((end - start + 1) / by);
   const out = Array(length);
   for (let i = 0; i < length; i++) {
      out[i] = start + i * by;
   }
   return out;
}


/**
 * Creates arrays with object indices, labels, classes and classNames without outliers.
 * @param {Object} dataset â€” JSON with dataset object.
 * @param {Array} outliers â€” array with object indices removed as outliers.
 * @returns {Array} array with four arrays â€”Â object indices, labels, classes and unique class names.
 */
export function handleOutliers(dataset, outliers) {
   let indices, classes, labels, classNames = [];
   if (outliers.length > 0) {
      // filter the outlier out
      const nAll = dataset.values.nrows;
      const sampleIndices = dataset.objindices;
      const n = nAll - outliers.length;
      indices = Array(n);
      classes = Array(n);
      labels = Array(n);
      let j = 0;
      for (let i = 0; i < nAll; i++) {
         if (outliers.includes(i)) continue;
         indices[j] = sampleIndices[i];
         classes[j] = dataset.classes[i];
         labels[j] = dataset.objlabels[i]
         if (classNames.length === 0 || !classNames.includes(classes[j])) {
            classNames.push(classes[j]);
         }
         j = j + 1;
      }
   } else {
      indices = dataset.objindices;
      labels = dataset.objlabels;
      classes = dataset.classes;
      classNames = dataset.classNames;
   }

   return [indices, labels, classes, classNames];
}


/**
 * Generates unique pseudo-hash number based on current time and date.
 * @returns  string with the hash.
 */
export function genhash() {
    const now = new Date();
    const uniqueId = `${now.getFullYear()}${(now.getMonth() + 1)
        .toString()
        .padStart(2, '0')}${now.getDate().toString().padStart(2, '0')}${now
        .getHours()
        .toString()
        .padStart(2, '0')}${now.getMinutes().toString().padStart(2, '0')}${now
        .getSeconds()
        .toString()
        .padStart(2, '0')}${now
        .getMilliseconds()
        .toString()
        .padStart(3, '0')}`;
    return uniqueId;
}


/**
 * Applies GLS model to a new dataset and adds additional attributes.
 *
 * @param {Promise} Module - WASM module (from Promise).
 * @param {Object} model - object with GLS model.
 * @param {Matrix} dataset - object with original dataset.
 * @param {Array} outliers - array with outliers.
 * @param {boolean} doPrep - apply or not preprocessing if preprocessing model is available.
 *
 * @returns {Object} object with PLS results
 */
export function app_gls_predict(Module, model, dataset, outliers, doPrep = false) {

   // get arrays with sample indices, classeas and object labels without outliers
   const [objindices, objlabels, classes] = handleOutliers(dataset, outliers);

   // if preprocesssing available apply it first

   const v = doPrep && model.prep && model.prep.ml && model.prep.ml.length > 0  ?
      prep_apply(Module, dataset.values, dataset.nresp, model.prep, false, true) :
      dataset.values;

   // make predictions and add labels and indices
   const r = gls_predict(Module, model, v, dataset.nresp, outliers);

   r['hash'] = genhash();
   r['objindices'] = objindices;
   r['objlabels'] = objlabels;
   r['varlabels'] = model.varlabels;
   r['resplabels'] = model.yvarlabels;

   return r;
}


/**
 * Applies PLS model to a new dataset and adds additional attributes.
 * @param {Promise} Module - WASM module (from Promise).
 * @param {Object} model - object with PLS model.
 * @param {Matrix} dataset - object with original dataset.
 * @param {Array} outliers - array with outliers.
 * @param {boolean} doPrep - apply or not preprocessing if preprocessing model is available.
 * @returns {Object} JSON with PLS results.
 */
export function app_pls_predict(Module, model, dataset, outliers, doPrep = false) {

   // get arrays with sample indices, classeas and object labels without outliers
   const [objindices, objlabels, classes] = handleOutliers(dataset, outliers);

   // if preprocesssing available apply it first
   const v = doPrep && model.prep && model.prep.ml && model.prep.ml.length > 0  ?
      prep_apply(Module, dataset.values, dataset.nresp, model.prep, false, true) :
      dataset.values;

   // make predictions and add labels and indices
   const r = pls_predict(Module, model, v, dataset.nresp, outliers);

   r['hash'] = genhash();
   r['objindices'] = objindices;
   r['objlabels'] = objlabels;
   r['varlabels'] = model.varlabels;

   if (dataset.nresp > 0) {
      r['Yref'] = (outliers && outliers.length > 0) ?
         dataset.values.subset(objindices.map(v => v + 1), Index.seq(1, dataset.nresp)) :
         dataset.values.subset(null, Index.seq(1, dataset.nresp));
   } else {
      r['Yref'] = null;
   }

   return r;
}


/**
 * Applies PCA model to a new dataset and adds additional attributes.
 * @param {Promise} Module - WASM module (from Promise).
 * @param {Object} model - object with PCA model.
 * @param {Matrix} dataset - object with original dataset.
 * @param {Array} outliers - array with outliers.
 * @param {boolean} doPrep - apply or not preprocessing if preprocessing model is available.
 * @returns {Object} JSON with PCA results
 */
export function app_pca_predict(Module, model, dataset, outliers, doPrep = false) {

   // get arrays with sample indices, classeas and object labels without outliers
   const [objindices, objlabels, classes] = handleOutliers(dataset, outliers);

   // if preprocesssing available apply it first
   const v = doPrep && model.prep && model.prep.ml && model.prep.ml.length > 0  ?
      prep_apply(Module, dataset.values, 0, model.prep, false, true) :
      dataset.values;


   // make predictions and add labels and indices
   const r = pca_predict(Module, model, v, outliers);
   r['hash'] = genhash();
   r['objindices'] = objindices;
   r['objlabels'] = objlabels;
   r['classes'] = classes;
   r['classNames'] = dataset.classNames;
   r['varlabels'] = dataset.varlabels;

   // model outcomes required for plots
   r['eigenvals'] = pcamodel_get(model, 'eigenvals');
   r['cNh'] = pcamodel_get(model, "cNh");
   r['cNq'] = pcamodel_get(model, "cNq");
   r['ch0'] = pcamodel_get(model, "ch0");
   r['cq0'] = pcamodel_get(model, "cq0");
   r['rNh'] = pcamodel_get(model, "rNh");
   r['rNq'] = pcamodel_get(model, "rNq");
   r['rh0'] = pcamodel_get(model, "rh0");
   r['rq0'] = pcamodel_get(model, "rq0");

   const n = model.nrows;
   r['T2lim'] =  (2 * (n - 1) / (n - 2)) * qf(0.95, 2, (n - 2));
   return r;
}


/**
 * Converts string or array of strings to number(s).
 * @param {Array|string} str - string or array of strings should be converted to numbers.
 * @param {string} dec - character for decimal delimeter.
 * @returns {Array|Number} number or array of numbers.
 */
function str2num(str, dec = '.') {

   if (Array.isArray(str) && str.length > 0) {
      return dec === '.' ? str.map(v => Number.parseFloat(v.trim())) :
         str.map(v => Number.parseFloat(v.trim().replace(dec, ".")));
   }

   return dec === '.' ? Number.parseFloat(str.trim()) :
      Number.parseFloat(str.trim().replace(dec, "."));
}


/**
 * Returns true if at least one value in array is NaN.
 * @param {Array} arr - array with values.
 * @returns logical value.
 */
function hasNaN(arr) {
   return arr.some(v => Number.isNaN(v));
}


/**
 * Removes white spaces and double quotes in a string (e.g. '" or """)
 * @param {String} s - string value.
 * @returns {String}
 */
function clearLabels(s) {
   return s.replace('\t', '').trim().replace(/^(['"])(.*)\1$/, '$2');
}


/**
 * Takes CSV content as a string, cleans it and splits to separate rows.
 * @param {String} csvString â€” string with CSV content.
 * @returns {Array} array of strings.
 */
function getCSVRows(csvString) {
   // remove BOM symbol if any
   let dataStr = csvString.replace(/^\uFEFF/gm, "").replace(/^\u00EF?\u00BB\u00BF/gm,"");

   // remove \r symbols
   dataStr = dataStr.replace(/\r/gm, "");

   // split string with data values into array of rows and filter empty rows
   return dataStr.split(/\n/).filter(v => v.trim().length > 1);
}


/**
 * Parse string with text data from a CSV file and create a matrix with values.
 * @param {string} strData - string with all data from the CSV file.
 * @param {string} sep - symbols used for separation of values (by default ',').
 * @param {string} header - type of header (first row in the data).
 * @param {boolean} hasLabels - logical, if first column must be treated as object labels or not.
 * @param {boolean} hasClassNames - logical, if column next to labels must be treated as class names or not.
 * @param {string} varvaluesName - name for variable values (e.g. Wavelength).
 * @param {string} varvaluesUnits - units for variable values (e.g. nm).
 * @param {number} nresp - number of response variables (columns), if -1 it will be set to the number of rows.
 * @returns {Object} JSON containing matrix with parsed values, header, values and decimal separators.
 */
export function parseCSV(strData, sep, header, hasLabels, hasClassNames, varvaluesName, varvaluesUnits, nresp = 0) {


   // set default separator symbol
   if (sep === undefined) {
      sep = ",";
   }

   if (!(sep === ',' || sep === ';')) {
      throw new Error('Values separator can be either comma or semicolon.')
   }

   // setup decimal separator
   const dec = sep === ";" ? "," : ".";

   // split string with data values into array of rows and filter empty rows
   let rows = getCSVRows(strData);

   if (rows.length < 1) {
      throw new Error('Could not find non-empty rows with values, check your CSV file and try again.');
   }

   // filter out rows that do not have values, only separators
   rows = rows.filter(v => v.replaceAll(sep, '').trim().length > 0);

   // get array with values from the first row
   const firstRowValues = rows[0].split(sep);
   if (firstRowValues.length < 2) {
      throw new Error("Found only one column, looks like your delimiter is not correct.");
   }

   // check if the first row contains header
   let startRow = 0;
   let headerLabels;
   const hasHeader = header !== 'no';
   if (hasHeader || (hasHeader === undefined && hasNaN(firstRow))) {
      headerLabels = firstRowValues;
      startRow = 1;
   }

   // number of rows and columns in future matrix
   const nrows = rows.length - startRow;
   if (nrows < 1) {
      throw new Error("Data file should contain at least 1 row with values.");
   }

   if (nresp === -1) {
      nresp = nrows;
   }

   // check if first column contains row labels
   let labelsVarname;
   let startColumn;
   let objlabels;
   let objindices = Array(nrows);

   if (hasLabels) {
      labelsVarname = hasHeader ? clearLabels(headerLabels[0]) : '';
      objlabels = Array(nrows);
      startColumn = 1;
   } else{
      labelsVarname = '';
      objlabels = [];
      startColumn = 0;
   }

   // check if next column contains class names
   let classLabelsVarname;
   let classNames = [];
   let classes;

   if (hasClassNames) {
      classLabelsVarname = hasHeader ? clearLabels(headerLabels[startColumn]) : '';
      classes = Array(nrows);
      startColumn = startColumn + 1;
   } else {
      classLabelsVarname = '';
      classes = [];
   }

   // compute number of columns
   const ncols = firstRowValues.length - startColumn;
   if (ncols < 1) {
      throw new Error("Data file should contain at least 1 column with values.");
   }

   // set rows of data as columns of matrix - it is faster
   const data = Matrix.zeros(ncols, nrows);

   // find symbol used for decimals separator
   for (let r = startRow; r < nrows + startRow; r++) {

      // add row/object index
      const rind = r - startRow;
      objindices[rind] = rind;

      // split the row string into array of values
      let rowValuesAll = rows[r].split(sep)

      // if the first element is row label add it to labels array and increase the offset
      if (hasLabels) {
         objlabels[rind] = rowValuesAll[0];
         rowValuesAll.shift()
      }

      // if the next element is class/group label add it to classes array and increase the offset
      if (hasClassNames) {
         const className = rowValuesAll[0];
         classes[rind] = className;
         rowValuesAll.shift()

         // also add it to array of unique class names
         if (classNames.length === 0 || classNames.indexOf(className) < 0) {
            classNames.push(className);
         }
      }

      // for the rest of values, trim spaces, replace separator symbol with dot (.) and convert to numbers
      const rowValues = dec === '.' ?
         rowValuesAll.map((v, i) => Number.parseFloat(v.trim())) :
         rowValuesAll.map((v, i) => Number.parseFloat(v.trim().replace(dec, ".")));

      if (rowValues.some(v => Number.isNaN(v))) {
         throw new Error("Some of the values can not be converted to numbers, check your CSV file and try again.");
      }

      if (rowValues.length != ncols) {
         throw new Error("Wrong number of values in row #" + r);
      }

      data.v.set(new Float64Array(rowValues), (r - startRow) * ncols);
   }


   // generate row labels if they are not provided
   if (!hasLabels || objlabels.length !== nrows) {
      objlabels = Array(nrows);
      for (let i = 0; i < nrows; i++) {
         objlabels[i] = 'O' + (i + 1);
      }
   }

   // remove possible extra quotes around the labels and classes
   objlabels = objlabels.map(v => clearLabels(v));
   classes = classes.map(v => clearLabels(v));
   classNames = classNames.map(v => clearLabels(v));

   // handle variable labels, indices and values
   let varlabels;
   let varindices = Array(ncols);
   let varvaluesTemp = Array(ncols);
   if (headerLabels && headerLabels.length === ncols + startColumn) {
      varlabels = headerLabels.map(v => clearLabels(v)).slice(startColumn);
      for (let i = 0; i < ncols; i++) {
         varindices[i] = i >= nresp ? i - nresp : i;
         varvaluesTemp[i] = i >= nresp ? i - nresp + 1: i + 1;
      }
   } else {
      varlabels = Array(ncols);
      for (let i = 0; i < ncols; i++) {
         varindices[i] = i >= nresp ? i - nresp : i;
         varlabels[i] = i >= nresp ? 'V' + (i - nresp + 1) : 'Y' + (i + 1);
         varvaluesTemp[i] = i >= nresp ? i - nresp + 1: i + 1;
      }
   }

   // parse variable labels as numeric values if it is expected
   let varvalues;
   if (header === 'values') {
      varvalues = dec === '.' ?
         varlabels.map((v, i) => i < nresp ? i : Number.parseFloat(v.trim())) :
         varlabels.map((v, i) => i < nresp ? i : Number.parseFloat(v.trim().replace(dec, ".")));
      if (varvalues.some(v => Number.isNaN(v))) {
         throw new Error("Some of the header values can not be converted to numbers, check your CSV file and try again.");
      }
   } else {
      varvalues = varvaluesTemp;
   }


   if (!Array.isArray(varvalues)) varvalues = [];
   if (!varvaluesName) varvaluesName = '';
   if (!varvaluesUnits) varvaluesUnits = '';

   varvaluesName = varvalues.length > 0 && varvaluesName.length > 0 ? varvaluesName : 'Variables';
   varvaluesUnits = varvalues.length > 0 && varvaluesUnits.length > 0 ? varvaluesUnits : '';
   const varAxisTitle = varvaluesName + (varvaluesUnits.length > 0 ? ', ' + varvaluesUnits : '');

   const varrev = varvalues[1] < varvalues[0];

   const out = {
      values: data.t(),
      nresp,
      header,
      hasLabels,
      hasClassNames,
      labelsVarname,
      classLabelsVarname,
      objlabels,
      objindices,
      varlabels,
      varvalues,
      varindices,
      varrev,
      classes,
      classNames,
      sep,
      dec,
      varvaluesName,
      varvaluesUnits,
      varAxisTitle
   };

   return out;
}


/**
 * Tries to detect if first row of CSV file contains header.
 * @param {Array} rows_num_values - arrat of CSV rows, split to separate values and converted to numbers.
 * @returns {boolean} true if header is detected.
 */
function detectHeader(rows_num_values) {

   // simple guesser based on presence of non-text values in the first row (check values 3 and 4)
   if (hasNaN(rows_num_values[0].slice(2))) {
      return true;
   }

   // if the values are numeric check
   // how different the values in the first row from the other ones
   // for columns starting fom 3
   const max_ncols = rows_num_values[0].length;
   const z_arr = [];
   for (let c = 2; c < max_ncols; c++) {

      // get values from the column and rows 2...4 (or less)
      let cv = [];
      for (let i = 1; i < rows_num_values.length; i++) {
         cv.push(rows_num_values[i][c]);
      }

      // compute absolute z-score and compare with 5
      const v1 = rows_num_values[0][c]
      const m = mean(cv);
      const s = sd(cv);
      const z = Math.abs((v1 - m) / s);
      z_arr.push(z);
   }

   // if all of them above 5 then most likely the first row is header
   return z_arr.every(v => v > 5);
}


/**
 * Detects symbol used to separate values in CSV file.
 * @param {Array} rows - array with rows (each row is a string).
 * @returns {Array} array with three values, symbols for values and decimals separators and number of columns.
 */
function detectSeparators(rows) {

   // guess values separator
   const sep1 = ',';
   const v11 = rows[0].split(sep1);
   const v12 = rows[1].split(sep1);
   const l11 = v11.length;
   const l12 = v12.length;

   const sep2 = ';';
   const v21 = rows[0].split(sep2);
   const v22 = rows[1].split(sep2);
   const l21 = v21.length;
   const l22 = v22.length;

   // check which of the two separators give longer and more consistent
   // arrays with values
   let ncols, v1, v2, sep;
   if (l11 > 1 && l12 > 1 && l11 === l12 && l11 > l21) {
      sep = sep1;
      ncols = l11;
      v1 = v11;
      v2 = v12;
   } else if (l21 > 1 && l22 > 1 && l21 === l22) {
      sep = sep2;
      ncols = l21;
      v1 = v21;
      v2 = v22;
   } else {
      throw new Error('Values in the CSV file should be separated by coma or by semicolon.');
   }

   // decimal separator
   const dec = sep === ',' ? '.' : ',';
   return [sep, dec, ncols];
}


/**
 * Parses CSV file string to detect main parameters (separators, header, presence of row and class labels).
 * @param {String} csvString - string with CSV file content.
 * @returns {Array} array with parameters.
 */
export function parseCSVParams(csvString, nresp = 0) {

   // split string with CSV content to separate rows and check size
   const rows = getCSVRows(csvString);
   const nrows = rows.length;
   if (nrows < 3) {
      throw new Error('It looks like the dataset has less then three rows, minimum three is needed, check your CSV file and try again.');
   }

   // detect values and decimal separators
   const [sep, dec, ncols] = detectSeparators(rows);
   const max_ncols = Math.min(ncols, 5);

   // split first several rows to values and keep maximum 5 values in each
   const max_nrows = Math.min(nrows, 5);
   let ncols_total = rows[0].split(sep).length;
   let rows_values = rows.slice(0, max_nrows).map(v => v.split(sep).slice(0, max_ncols));

   // additionally clear the values
   rows_values = rows_values.map(v => v.map(v => clearLabels(v)));

   // turn values to numeric and check that values starting from row 2 and column 3 are numeric
   const rows_num_values = rows_values.map(v => str2num(v.slice(), dec));
   for (let i = 1; i < max_nrows; i++) {
      if (hasNaN(rows_num_values[i].slice(2))) {
         throw new Error('Some of the data values expected to be numeric are not, check your CSV file.');
      }
   }

   // detect if the data has header
   const hasHeader = detectHeader(rows_num_values);
   let headerShift = 0;

   const r1 = hasHeader ? 1 : 0;
   const r2 = r1 + 1;

   // values from first column of rows 2 and 3 starting from header
   const v21 = rows_values[r1][0];
   const v31 = rows_values[r2][0];

   // numeric values from first column of rows 2 and 3
   const nv21 = rows_num_values[r1][0];
   const nv31 = rows_num_values[r2][0];

   // numeric values from second column of rows 2 and 3
   const nv22 = rows_num_values[r1][1];
   const nv32 = rows_num_values[r2][1];

   // detect columns with row and class labels
   let hasLabels = false;
   let hasClassLabels = false;
   if (Number.isNaN(nv21) && Number.isNaN(nv31) && v21 !== v31) {
      // guess if row labels present: if first value of rows 2 and 3 are not numbers
      // and are not equal to each other than they are possible row labels
      hasLabels = true;
      headerShift = headerShift + 1;
   } else if (Number.isNaN(nv22) && Number.isNaN(nv32) && v21 !== v31) {
      // now check if values from the second column are non numeric,
      // and at the same time values from the first column are not equal, if so
      // the second column possible contains class names and hence the first column
      // consist of row labels
      hasLabels = true;
      hasClassLabels = true;
      headerShift = headerShift + 2;
   }


   // guess class labels - we check next column and if both values of rows 2 and 3
   // are not numeric, we assume they are class labels
   if (!hasClassLabels) {
      const ncol = hasLabels ? 1 : 0;

      // numeric values from first column of rows 2 and 3
      const nv21 = rows_num_values[r1][ncol];
      const nv31 = rows_num_values[r2][ncol];

      hasClassLabels = hasNaN([nv21, nv31]);
      headerShift = headerShift + hasClassLabels;
   }

   // gues if header contains text labels of numeric values
   let header = 'no';
   if (hasHeader) {
      const v1 = headerShift > 0 ? rows_values[0].slice(headerShift + nresp) : rows_values[nresp];
      const nv1 = headerShift > 0 ? rows_num_values[0].slice(headerShift + nresp) : rows_num_values[nresp];
      header = hasNaN(nv1) ? 'labels' : 'values';
   }

   return [sep, hasLabels, hasClassLabels, header, ncols_total];
}


/**
 * Converts dataset object to CSV string.
 * @param {Object} dataset - object with dataset.
 * @returns {String}
 */
export function toCSV(dataset) {

   // get values then transpose
   const values = dataset.values.t();
   const sep = dataset.sep;
   const dec = dataset.dec;

   let header_values = null;
   if (dataset.header !== 'no') {
      header_values = dataset.header === 'values' ?
         (dataset.nresp  && dataset.nresp > 0 ?
            dataset.varlabels.slice(0, dataset.nresp).concat(dataset.varvalues.slice(dataset.nresp)) : dataset.varvalues.slice())
         : dataset.varlabels.slice();
   }

   if (header_values && dataset.hasClassNames) {
      header_values = [dataset.classLabelsVarname, ...header_values];
   }

   if (header_values && dataset.hasLabels) {
      header_values = [dataset.labelsVarname, ...header_values];
   }

   const hasClassNames = dataset.classes && dataset.classes.length === values.ncols;
   const hasLabels = dataset.objlabels && dataset.objlabels.length === values.ncols;
   let csvString = header_values ? header_values.join(sep) : '';

   for (let i = 0; i < values.ncols; i++) {
      let rowStr = values.getcolref(i + 1).join(sep);

      if (dec !== '.') {
         rowStr = rowStr.replace('.', dec);
      }

      if (hasClassNames) {
         rowStr = dataset.classes[i] + dataset.sep + rowStr;
      }

      if (hasLabels) {
         rowStr = dataset.objlabels[i] + dataset.sep + rowStr;
      }

      csvString = csvString + '\n' + rowStr;
   }
   csvString = csvString + '\n';

   if (dec !== '.') {
      csvString = csvString.replace(/\./g, dec);
   }

   return csvString;
}


/**
 * Convert dataset to CSV and starts downloading process.
 * @param {Object} dataset - JSON with dataset.
 * @param {String} filename - filename (without extention).
 */
export function downloadData(dataset, filename) {
   const csvString = toCSV(dataset);
   const blob = new Blob([csvString], { type: 'text/csv' });
   const url = window.URL.createObjectURL(blob)
   const a = document.createElement('a')
   a.setAttribute('href', url)
   a.setAttribute('download', `${filename}.csv`);
   a.click()
}


/**
 * Start downloading of JSON object as a file.
 * @param {Object} obj - JSON with object to download.
 */
export function downloadJSON(obj, fileName) {

   const blob = new Blob([JSON.stringify(obj, replacer)], { type: 'application/json' });
   const url = window.URL.createObjectURL(blob)
   const a = document.createElement('a')
   a.setAttribute('href', url)
   a.setAttribute('download', fileName + '.json');
   a.click()
}


/**
 * Create a new dataset object by excluding variables from the original one.
 * @param {Object} dataset - dataset with original values.
 * @param {Array} varlist - list of variables to exclude.
 * @returns {Object}
 */
export function excludeVars(dataset, varlist) {
   if (!dataset) return;
   const nresp = dataset.nresp ? dataset.nresp : 0;
   const values = dataset.values;
   const varvalues = dataset.varvalues;
   const varlabels = dataset.varlabels;
   const varindices = dataset.varindices;

   const nrows = values.nrows;
   const ncols = values.ncols;
   const ncolsNew = ncols - varlist.length
   const valuesNew = Matrix.zeros(nrows, ncolsNew);
   const varvaluesNew = Array(ncolsNew);
   const varlabelsNew = Array(ncolsNew);
   const varindicesNew = Array(ncolsNew);

   let c = 0;
   for (let i = 0; i < nresp; i++) {
      const offset_0 = nrows * i;
      const offset_1 = nrows * c;
      valuesNew.v.set(values.v.slice(offset_0, offset_0 + nrows), offset_1);
      varvaluesNew[c] = varvalues[i];
      varlabelsNew[c] = varlabels[i];
      varindicesNew[c] = varindices[i];
      c = c + 1;
   }

   for (let i = nresp; i < ncols; i++) {
      if (varlist.includes(i - nresp)) continue;
      const offset_0 = nrows * i;
      const offset_1 = nrows * c;
      valuesNew.v.set(values.v.slice(offset_0, offset_0 + nrows), offset_1);
      varvaluesNew[c] = varvalues[i];
      varlabelsNew[c] = varlabels[i];
      varindicesNew[c] = varindices[i];
      c = c + 1;
   }

   return {
      values: valuesNew,
      header: dataset.header,
      hasLabels: dataset.hasLabels,
      hasClassNames: dataset.hasClassNames,
      classNames: dataset.classNames,
      labelsVarname: dataset.labelsVarname,
      classLabelsVarname: dataset.classLabelsVarname,
      objlabels: dataset.objlabels,
      objindices: dataset.objindices,
      varlabels : varlabelsNew,
      varvalues : varvaluesNew,
      varindices: varindicesNew,
      varrev: dataset.varrev,
      classes: dataset.classes,
      responses: dataset.responses,
      classNames: dataset.classNames,
      sep: dataset.sep,
      dec: dataset.dec,
      varvaluesName: dataset.varvaluesName,
      varvaluesUnits: dataset.varvaluesUnits,
      varAxisTitle: dataset.varAxisTitle,
      fileName: dataset.fileName,
      nresp: nresp
   };
}


/**
 * Create a new dataset object by excluding outliers from the original one.
 * @param {Object} dataset - dataset with original values.
 * @param {Array} outliers - list of outliers to exclude.
 * @returns {Object}
 */
export function excludeOutliers(dataset, outliers) {

   if (!outliers || outliers.length === 0) return dataset;
   const [objindices, objlabels, classes, classNames] = handleOutliers(dataset, outliers);
   const values = dataset.values.subset(objindices.map(v => v + 1), []);

   return {
      values: values,
      objlabels: objlabels,
      objindices: objindices,
      classes: classes,
      classNames: classNames,

      header: dataset.header,
      hasLabels: dataset.hasLabels,
      hasClassNames: dataset.hasClassNames,
      classNames: dataset.classNames,
      labelsVarname: dataset.labelsVarname,
      classLabelsVarname: dataset.classLabelsVarname,
      varlabels : dataset.varlabels,
      varvalues : dataset.varvalues,
      varindices: dataset.varindices,
      varrev: dataset.varrev,
      responses: dataset.responses,
      sep: dataset.sep,
      dec: dataset.dec,
      varvaluesName: dataset.varvaluesName,
      varvaluesUnits: dataset.varvaluesUnits,
      varAxisTitle: dataset.varAxisTitle,
      fileName: dataset.fileName,
      nresp: dataset.nresp
   };
}


/**
 * Converts array of numbers to string with intervals (e.g. [1, 2, 3, 4, 10, 11, 12] -> "1-4, 10-12").
 * @param {Array} arr - array of numbers.
 * @returns {String}
 */
export function convertToIntervals(arr) {
  if (!arr.length) return '';

  // Sort the array in ascending order
  arr.sort((a, b) => a - b);

  let result = [];
  let start = arr[0];
  let end = start;

  for (let i = 1; i < arr.length; i++) {
    if (arr[i] === end + 1) {
      // continue the sequence
      end = arr[i];
    } else {
      // sequence broken, push current range to result
      if (start === end) {
        result.push(`${start}`);
      } else {
        result.push(`${start}-${end}`);
      }
      // reset start and end for the next sequence
      start = arr[i];
      end = start;
    }
  }

  // push the last range or number
  if (start === end) {
    result.push(`${start}`);
  } else {
    result.push(`${start}-${end}`);
  }

  return result.join(" ");
}


/**
 * Manual replacer for JSON.stringify() to handle matrices, vectors and typed arrays correctly.
 * @param {string} key - key
 * @param {Object} value - value
 * @returns {Object} JSON with proper structure, so it can be converted back by reviver.
 */
export function replacer(key, value) {


   // objects of Matrix class from mdatools/arrays
   if (ismatrix(value)) {
      return {
         __type: 'matrix',
         nrows: value.nrows,
         ncols: value.ncols,
         v: Array.from(value.v)
      };
   }

   // objects of Vector class from mdatools/arrays
   if (isvector(value)) {
      return {
         __type: 'vector',
         length: value.length,
         v: Array.from(value.v)
      };
   }

   // Typed arrays
   if (ArrayBuffer.isView(value)) {
      return {
         __type: value.constructor.name,
         data: Array.from(value)
      };
   }

   // NIMCA model
   if (value instanceof NIMCA) {
      return value.asJSON()
   }


   return value;
}


/**
 * Manual reviver for parseJSON() to handle matrices, vectors and typed arrays correctly
 * @param {string} key - key
 * @param {JSON} value - value
 * @returns {Object} correctly recovered object.
 */
export function reviver(key, value) {

   if (value && typeof value === "object" && value.__type) {

      if (value.__type === 'matrix') {
         return matrix(value.v, value.nrows, value.ncols);
      }

      if (value.__type === 'vector') {
         return vector(value.v);
      }

      if (value.__type === 'NIMCA') {
         return NIMCA.fromJSON(value);
      }

      if (value.__type === 'NIMCARes') {
         return null;
      }

      const TypedArrayConstructor = globalThis[value.__type];
      if (typeof TypedArrayConstructor === "function") {
         return new TypedArrayConstructor(value.data);
      }
    }

    return value;
}


/**
 * Converts date/time object to string.
 * @param {Date} date
 * @returns {string}
 */
export function date2str(date) {
   const year = date.getFullYear();
   const month = String(date.getMonth() + 1).padStart(2, '0'); // Months are 0-based
   const day = String(date.getDate()).padStart(2, '0');
   const hours = String(date.getHours()).padStart(2, '0');
   const minutes = String(date.getMinutes()).padStart(2, '0');
   const seconds = String(date.getSeconds()).padStart(2, '0');

   return `${year}${month}${day}${hours}${minutes}${seconds}`;
}


/**
 * Iterate over top level keys of JSON object and produce another JSON
 * with the same keys and values derived by applying a function to the original values.
 * @param {JSON} obj - original object.
 * @param {function} f - lambda functon with two parameters, object and key, which returns a value.
 * @returns {Object} preprocessed JSON
 */
export function objmap(obj, f) {
   return Object.keys(obj).reduce((acc, id) => {
      acc[id] = f(obj, id);
      return acc;
   }, {});
}


/**
 * Checks if JSON is empty.
 * @param {Object} obj JSON under questions.
 * @returns {boolean} true if is empty.
 */
export function isempty(obj) {
   return !obj || Object.keys(obj).length < 1;
}


/**
 * Converts byte to individual bit values.
 * @param {*} byte
 * @returns
 */
function byteToBits(byte) {
  return Array.from({ length: 8 }, (_, i) => (byte >> (7 - i)) & 1).reverse();
}


/**
 * Reads several bytes from binary data.
 * @param {DataView} - view interface for array buffer with binary data.
 * @param {number} len - number of bytes to read.
 * @param {number} offset - initial offset.
 * @returns {String} the read bytes as characters (string).
 */
function readString(view, len, offset) {
   let str = "";
   for (let i = 0; i < len; i++) {
      const charCode = view.getUint8(offset++);
      if (charCode === 0) break;
      str += String.fromCharCode(charCode);
   }
   return str;
}


/**
 * Parser for SPC file header.
 * @param {DataView} - view interface for array buffer with header as binary data.
 * @returns {Object} JSON with header elements and values.
 */
function parseSPCHeader(view) {

   const experiments = [
      "General", "Gas Chromatogram", "General Chromatogram", "HPLC Chromatogram",
      "NIR Spectrum", "UV-VIS Spectrum", "* reserved *", "X-ray diffraction spectrum",
      "Mass Spectrum", "NMR Spectrum", "Raman Spectrum", "Fluorescence Spectrum",
      "Atomic Spectrum", "Chroatography Diode Array Data"
   ];

   const xaxisUnits = ["A.U.", "cm^â€“1", "Âµm", "nm", "s", "min", "Hz", "KHz", "MHz", "M/z", "ppm", "days",
      "years", "cm^â€“1", "eV", "", "", "", "", "F", "C", "K", "", "msec", "Âµsec", "nsec", "GHz", "cm",
      "m", "mm", "h", ""
   ];

   const xaxisNames = [ "Arbitrary", "Wavenumber", "Micrometers", "Nanometers", "Seconds",
      "Minutes", "Hertz", "Kilohertz", "Megahertz", "Mass", "Parts per million", "Days", "Years",
      "Raman shift", "Electron Volts", "X,Y,Z text labels in fcatxt (old 4Dh version only)",
      "Diode Number", "Channel", "Degrees", "Temperature", "Temperature", "Temperature",
      "Data Points", "Milliseconds", "Microseconds", "Nanoseconds", "Gigahertz",
      "Centimeters", "Meters", "Millimeters", "Hours", "Double interferogram (no display labels)"
   ];

   const yaxisNames = [
      "Arbitrary Intensity",         // 0
      "Interferogram",               // 1
      "Absorbance",                  // 2
      "Kubelka-Munk",                // 3
      "Counts",                      // 4
      "Volts",                       // 5
      "Degrees",                     // 6
      "Milliamps",                   // 7
      "Millimeters",                 // 8
      "Millivolts",                  // 9
      "Log (1/R)",                   // 10
      "Percent",                     // 11
      "Intensity",                   // 12
      "Relative Intensity",          // 13
      "Energy",                      // 14
      null,                          // 15 â€“ Not used
      "Decibel",                     // 16
      null,                          // 17 â€“ Not used
      null,                          // 18 â€“ Not used
      "Temperature (F)",             // 19
      "Temperature (C)",             // 20
      "Temperature (K)",             // 21
      "Index of Refraction [N]",     // 22
      "Extinction Coeff. [K]",       // 23
      "Real",                        // 24
      "Imaginary",                   // 25
      "Complex",                     // 26
      /* ... values 27â€“127 are undefined/unused in the spec ... */
      /* Skip to 128+ */
   ];

   const yaxisNamesExt = [
      "Transmission",                // 128
      "Reflectance",                 // 129
      "Arbitrary or Single Beam with Valley Peaks", // 130
      "Emission"                     // 131
   ];

   let offset = 0;

   // flag bits
   const ftflgs = view.getUint8(offset); offset += 1;

   // file version
   const fversn = view.getUint8(offset); offset += 1;
   if (fversn === 0x4D) {
      throw new Error('SPC file has old format which is not supported.');
   }
   const le = (fversn === 0x4B); // little endian

   const fexper = view.getUint8(offset); offset += 1;
   const fexp = view.getInt8(offset); offset += 1;
   const fnpts = view.getUint32(offset, le); offset += 4;
   const ffirst = view.getFloat64(offset, le); offset += 8;
   const flast = view.getFloat64(offset, le); offset += 8;
   const fnsub = view.getUint32(offset, le); offset += 4;
   const fxtype = view.getUint8(offset); offset += 1;
   const fytype = view.getUint8(offset); offset += 1;
   const fztype = view.getUint8(offset); offset += 1;
   const fpost = view.getUint8(offset); offset += 1;
   const fdate = view.getUint32(offset, le); offset += 4;
   const fres = readString(view, 9, offset); offset += 9;
   const fsource = readString(view, 9, offset); offset += 9;
   const fpeakpt = view.getUint16(offset, le); offset += 2;
   const fspare = Array.from({ length: 8 }, () => {const v = view.getFloat32(offset, le); offset += 4; return v;});
   const fcmnt = readString(view, 130, offset); offset += 130;
   const fcatxt = readString(view, 30, offset); offset += 30;
   const flogoff = view.getUint32(offset, le); offset += 4;

   const [TSPREC, TCGRAM, TMULTI, TRANDM, TORDRD, TALABS, TXYXYS, TXVALS] = byteToBits(ftflgs);
   const xaxisName = fxtype === 255 ? xaxisNames[31] : xaxisNames[fxtype];
   const xaxisUnit = fxtype === 255 ? xaxisUnits[31] : xaxisUnits[fxtype];
   const yaxisName = fytype <= 26 ? yaxisNames[fytype] :
      (fytype >= 128 && fytype <= 131 ? yaxisNamesExt[fytype] : 'Unknown');

   return {
      ftflgsv: ftflgs,
      ftflgs: {TSPREC, TCGRAM, TMULTI, TRANDM, TORDRD, TALABS, TXYXYS, TXVALS},
      fexper,
      fexp,
      fnpts,
      ffirst,
      flast,
      fnsub,
      fxtype,
      fytype,
      fztype,
      fpost,
      fdate,
      fres,
      fsource,
      fpeakpt,
      fspare,
      fcmnt,
      fcatxt,
      flogoff,
      xaxisName,
      xaxisUnit,
      yaxisName,
   };
}


/**
 * Parser for SPC file sub-header.
 * @param {DataView} - view interface for array buffer with header as binary data.
 * @param {number} - offset to locate the particular subheader.
 * @returns {Object} JSON with header elements and values.
 */
function parseSPCSubHeader(view, offset) {
   const flags = byteToBits(view.getUint8(offset)); offset += 1;
   const exp = view.getInt8(offset); offset += 1;
   const index = view.getUint16(offset, true); offset += 2;
   const time = view.getFloat32(offset, true); offset += 4;
   const next = view.getFloat32(offset, true); offset += 4;
   const noise = view.getFloat32(offset, true); offset += 4;
   const nPoints = view.getInt32(offset, true); offset += 4;
   const nScans = view.getInt32(offset, true); offset += 4;
   const wAxis = view.getFloat32(offset, true); offset += 4;

   return {
      flags, exp, index, time, next, noise, nPoints, nScans, wAxis
   }
}


/**
 * SPC file reader.
 * @param {Object} dataFile - File object (from form element).
 * @returns {Object} JSON with header, x and y-values, and object labels.
 */
async function readSPCFile(dataFile) {

   // check extention
   const fileExt = dataFile.name.slice(-3);
   if (fileExt.toLowerCase() !== 'spc') {
      throw new Error(`One of the selected files is not SPC (${dataFile.name}).`);
   }

   // get filename, read data to array buffer and add a view
   const fileName = dataFile.name.slice(0, -4);
   const buffer = await dataFile.arrayBuffer();
   const view = new DataView(buffer);

   // read and parse header
   let offset = 0;
   const hdr = parseSPCHeader(view, offset); offset += 512;
   const npts = hdr.fnpts;
   if (npts < 2 ) {
      throw new Error(`Error reading SPC file (${dataFile.name}).`);
   }

   // process x-values
   let xvalues = new Array(npts);
   if (hdr.ftflgs.TXVALS) {
      // they are given explicitly
      for (let i = 0; i < npts; i++) {
         const v = view.getFloat32(offset, true); offset = offset + 4
         xvalues[i] = v;
      }
   } else {
      // generate them
      const dx = (hdr.flast - hdr.ffirst) / (npts - 1);
      for (let i = 0; i < npts; i++) {
         xvalues[i] = hdr.ffirst + i * hdr.flast;
      }
   }

   // process spectral values
   const yvalues = new Float64Array(npts * hdr.fnsub);
   for (let s = 0; s < hdr.fnsub; s++) {
      const shdr = parseSPCSubHeader(view, offset); offset = offset + 32;
      for (let i = 0; i < npts; i++) {
         const v = view.getFloat32(offset, true); offset = offset + 4;
         yvalues[s * npts + i] = v;
      }
   }

   // generate object labels
   const objlabels  = [];
   if (hdr.fnsub > 1) {
      const nums = seq(1, hdr.fnsub);
      nums.forEach(v => { objlabels.push(fileName + '_' + v);});
   } else {
      objlabels.push(fileName)
   }

   return {hdr, xvalues, yvalues,  objlabels};
}


/**
 * SPC files reader.
 * @param {Object} dataFiles - FileList object (from form element).
 * @returns {Object} JSON with dataset, where spectra from individual files are combined into matrix.
 */
export async function readSPCFiles(dataFiles) {

   if (!(dataFiles instanceof FileList || Array.isArray(dataFiles))) {
      throw new Error('readSPCFiles: expected FileList or array of files.');
   }

   const nFiles = dataFiles.length;
   const spectra = [];
   let objlabels = [];
   let nSpectra = [];

   // read first file
   const first = await readSPCFile(dataFiles[0]);
   const nPoints = first.xvalues.length;
   spectra.push(first.yvalues);
   objlabels = objlabels.concat(first.objlabels);
   nSpectra.push(first.hdr.fnsub);

   // process the rest
   if (nFiles > 1) {
      for (let n = 1; n < nFiles; n++) {
         const out = await readSPCFile(dataFiles[n])
         if (out.xvalues.length !== nPoints) {
            throw new Error('Number of spectral values in SPC files is different.');
         }
         spectra.push(out.yvalues);
         objlabels = objlabels.concat(out.objlabels);
         nSpectra.push(out.hdr.fnsub);
      }
   }

   // merge all spectra together
   const nSpectraTot = nSpectra.reduce((v, acc) => v + acc, 0);

   const values = new Float64Array(nSpectraTot * nPoints);
   let offset = 0;
   for (let i = 0; i < spectra.length; i++) {
      values.set(spectra[i].slice(), offset);
      offset += nSpectra[i] * nPoints;
   }

   const valuesMat = new Matrix(values, nPoints, nSpectraTot);
   const varindices = seq(0, nPoints - 1);
   const objindices = seq(0, nSpectraTot - 1);
   const varlabels = first.xvalues.map(v => v.toFixed(1));

   return {
      values: valuesMat.t(),
      nresp: 0,
      header: 'values',
      hasLabels: true,
      hasClassNames: false,
      labelsVarname: '',
      classLabelsVarname: '',
      classes: [],
      classNames: [],
      objlabels,
      objindices,
      varlabels,
      varvalues: first.xvalues,
      varindices,
      varev: first.xvalues[1] < first.xvalues[0],
      sep: ',',
      dec: '.',
      varvaluesName: first.hdr.xaxisName,
      varvaluesUnits: first.hdr.xaxisUnit,
      varAxisTitle: '',
      fileName: `${nFiles} SPC files`
   }

      // objlabels,
      // objindices,
      // varlabels,
      // varvalues,
      // varindices,
      // varrev,
      // classes,

}


/**
 * Computes 95% confidence interval for a given alpha value and sample size using Normal distribution approximation.
 * @param {number} n - sample size.
 * @param {number} alpha - significance level (between 0 and 1).
 * @returns {Array} array with lower and upper boundaries of the interval.
 */
export function getCI(n, alpha) {
   const cl = 1 - alpha / 100;
   const sigma = Math.sqrt(n * cl * (1 - cl));
   const em = 1.96 * sigma / n;
   return [cl, em];
}


/**
 * Checks if array is correct and all values are within given range.
 * @param {Array} arr - array.
 * @param {number} minVal - smallest expected value.
 * @param {number} maxVal - largest expected value.
 * @returns {boolean} result of check.
 */
export function isArrCorrect(arr, minVal, maxVal) {
   if (!Array.isArray(arr)) return false;
   if (arr.length === 0) return true;

   for (let i = 0; i < arr.length; i++) {
      const v = arr[i];
      if (v < minVal || v > maxVal) return false;
   };

   return true;
}


/**
 * Takes dataset with class column and adds dummy response column for each class.
 * @param {Object} - JSON with original dataset.
 * @returns {Object} - JSON with new dataset.
 */
export function addDummy(dataset, reduced = 1) {
   if (!dataset.classNames || dataset.classNames.length === 0) return dataset;

   const nresp = dataset.classNames.length - reduced;
   const npred = dataset.values.ncols;
   const nrows = dataset.values.nrows;
   let offset = nresp * nrows;

   const values = Matrix.zeros(nrows, npred + nresp);
   values.v.set(dataset.values.v, offset)

   for (let i = 0; i < nresp; i++) {
      offset = i * nrows;
      const className = dataset.classNames[i];
      for (let j = 0; j < nrows; j++) {
         values.v[offset + j] = dataset.classes[j] === className ? 1 : -1;
      }
   }

   const varlabels = [...dataset.classNames, ...dataset.varlabels];
   const varvalues = [...seq(1, nresp), ...dataset.varvalues];
   const varindices = [...seq(0, nresp - 1), ...dataset.varindices];

   return {

      values,
      nresp,
      varlabels,
      varvalues,
      varindices,

      header: dataset.header,
      hasLabels: dataset.hasLabels,
      hasClassNames: dataset.hasClassNames,
      labelsVarname: dataset.labelsVarname,
      classLabelsVarname: dataset.classLabelsVarname,
      objlabels: dataset.objlabels,
      objindices: dataset.objindices,
      varrev: dataset.varrev,
      classes: dataset.classes,
      classNames: dataset.classNames,
      sep: dataset.sep,
      dec: dataset.dec,
      varvaluesName: dataset.varvaluesName,
      varvaluesUnits: dataset.varvaluesUnits,
      varAxisTitle: dataset.varAxisTitle
   };
}