<script>
   /**************************************************************/
   /* AppContainer for applying                                  */
   /* -------------------------                                  */
   /* app container for applying preprocessing model to new data */
   /**************************************************************/

   import { untrack } from 'svelte';

   import AppPlots from '../shared/AppPlots.svelte';
   import ModelLoader from '../shared/ModelLoader.svelte';
   import DataLoader from '../shared/DataLoader.svelte';
   import { getShortData } from '../utils';

   import { replacer } from '../../../shared/utils.js';
   import { getTrim } from '../../../shared/wasm/dist/prep';

   // WASM promise and session object from parent app
   let {WASM, backupSession, session = $bindable()} = $props();

   // get part of the session for this tab
   const s = $derived(session['apply']);

   // effect to track changes in dataset and models
   $effect(() => {
      if (s.model === null) {
         s.rawdata = null;
         s.rawdataShort = null;
         s.prepdata = null;
      }
   });

   // effect for getting left and right values from model
   $effect(() => {
      if (s.model) {
         // we do not need to set left/right for apply tab
         // as data is truncated
         //[s.left, s.right] = getTrim(s.model);
         s.left = -1;
         s.right = -1;
      }
   });

   $effect(() => {
      if (s.rawdata === null) {
         s.prepdata = null;
         s.prepdataShort = null;
      }
   });

   $effect(() => {
      s.rawdataShort = s.rawdata ? getShortData(s.rawdata, 30) : null;
   });

   $effect(() => {
      s.prepdataShort = s.prepdata ? getShortData(s.prepdata, 30) : null;
   });


   $effect(() => {
      const sessionStr = untrack(() => JSON.stringify(session, replacer));
      backupSession(sessionStr, s.rawdata, s.prepdata, s.model);
   });

</script>

<div class="app-container">

   <!-- left column with main app controls -->
   <div class="app-controls">

      <div class="model-loader">
         <ModelLoader bind:model={s.model} bind:left={s.left} bind:right={s.right}/>
      </div>

      {#if s.model}
      <div class="data-loader">
         <DataLoader {WASM} bind:rawdata={s.rawdata} bind:prepdata={s.prepdata} model={s.model} left={s.left} right={s.right}/>
      </div>
      {/if}
   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if !s.rawdata}
         <div class="help-text">
            <p>
               In this tab you can apply preprocessing model, developed in <em>Create</em> tab to a new dataset.
            </p>
            {#if !s.model}
            <p><strong>Step 1.</strong></p>
            <p>Load JSON file with a model you saved from <em>Create</em> tab.</p>
            {:else}
            <p><strong>Step 2.</strong></p>
            <p>Load your data from single CSV file or single/multiple SPC files. See details about data file in the <em>Create</em> tab.</p>
            {/if}
         </div>
      {:else if s.rawdataShort && s.prepdataShort}
         {#key s.rawdataShort }
         <AppPlots
            rawdata={s.rawdataShort}
            prepdata={s.prepdataShort}
            left={s.left}
            right={s.right}
         />
         {/key}
      {/if}
   </div>
</div>

