<script>
   /*************************************************************/
   /* Widget for scaling                                        */
   /* -----------------------------------                       */
   /*************************************************************/

   import { Container, RangeDiscrete } from 'svelte-controls-basic';
   import PrepTemplate from './PrepTemplate.svelte';

   let {
      params = $bindable([3, 5]),
      varind = $bindable(-1),
      settings = $bindable(),
      nvars = 0,
      ...others
   } = $props();


   // initial settings
   const title = 'Spike removal';

   $effect(() => {
      settings = params[0] + '/' + params[1];
   });
</script>


<PrepTemplate id="scale" {title} {settings} {...others}>
   <Container label="Window">
   <RangeDiscrete bind:value={params[0]} min={3} max={15} step={2} />
   </Container>
   <Container label="Threshold">
   <RangeDiscrete bind:value={params[1]} min={3} max={50} />
   </Container>
</PrepTemplate>

