<script>
   /*************************************************************/
   /* Widget for scaling                                        */
   /* -----------------------------------                       */
   /*************************************************************/

   import { Container, Select } from 'svelte-controls-basic';
   import PrepTemplate from './PrepTemplate.svelte';

   let {
      params = $bindable([1, 0]),
      varind = $bindable(-1),
      settings = $bindable(),
      nvars = 0,
      ...others
   } = $props();

   // type to num maps
   const centerMap = {'no': 0, 'mean': 1, 'median': 2};
   const scaleMap = {'no': 0, 'sd': 1, 'iqr': 2, 'range': 3, 'pareto': 4};
   const centers = Object.keys(centerMap);
   const scales = Object.keys(scaleMap);

   // initial settings
   let center = $state(centers[params[0]]);
   let scale = $state(scales[params[1]]);
   const title = 'Scaling';

   $effect(() => {
      settings = center + '/' + scale;
   });

   // effect to trace changes
   $effect(() => { if (center) params[0] = centerMap[center]});
   $effect(() => { if (scale) params[1] = scaleMap[scale]});
</script>


<PrepTemplate id="scale" {title} {settings} {...others}>
   <Container label="Center">
   <Select bind:value={center} options={centers} />
   </Container>
   <Container label="Scale">
   <Select bind:value={scale} options={scales} />
   </Container>
</PrepTemplate>

