<script>
   /*************************************************************/
   /* Widget for trimming tails                                 */
   /* -----------------------------------                       */
   /*************************************************************/

   import { Container, Range } from 'svelte-controls-basic';
   import PrepTemplate from './PrepTemplate.svelte';

   let {
      params = $bindable(),
      varind = $bindable(),
      settings = $bindable(),
      isOpen,
      left,
      right,
      nvars,
      ...others
   } = $props();


   // initial settings
   const title = 'Trim tails';
   let show = $state(false);
   const delta = $derived(nvars ? Math.round(nvars * 0.10) : 1);


   // effect to make sure tha params values are correct
   $effect(() => {
      if (isOpen) {
         if (params[0] === undefined || params[0] === null || params[0] < left || params[0] > right - delta) params[0] = left;
         if (params[1] === undefined || params[1] === null || params[1] > right || params[1] < left + delta) params[1] = right;
         varind[1] = params[0];
         varind[2] = params[1];
         show = true;
      } else {
         varind[1] = -1;
         varind[2] = -1;
         show = false;
      }
   });


   $effect(() => {
      settings = 'left: ' + params[0] + ' right: ' + params[1];
   });

</script>

<PrepTemplate id="norm" {title} {settings} {isOpen} {...others}>
   {#if show}
   <Container label="Left">
   <Range bind:value={params[0]} min={left} max={right - delta} step={1} decNum={0} />
   </Container>
   <Container label="Right">
   <Range bind:value={params[1]} min={left + delta} max={right} step={1} decNum={0} />
   </Container>
   {/if}
</PrepTemplate>
