// default plot settings
export const MARGINS = [1.0, 1.1, 0.45, 0.4];

// default colors for SIMCA categories
export const CAT_COLORS = {
   'regular': '#2679B2',
   'extreme': '#F2B825',
   'outlier': '#D22C2F', 'outliers': '#D22C2F',
   'alien': '#2679B2', 'aliens': '#2679B2',
   'external': '#D22C2F'
};

// default colors for series
export const MARKERS = {'cal': 1, 'pv': 2, 'test': 2, 'new': 2};
export const LINE_COLORS = {'cal': '#2679B2', 'pv': '#D22C2F', 'test': '#a05e5e', 'new': '#5e85a0'};
export const FACE_COLORS = {'cal': '#f0f8ff', 'pv': '#f4e5e5', 'test': '#fff0f8', 'new': '#f0f8ff'};
export const FACE_TCOLORS = {'cal': '#2679B230', 'pv': '#D22C2F30', 'test': '#f05e5e30', 'new': '#5e85a030'};
export const LOADINGS_LINE_COLOR = '#808090';
export const LOADINGS_TEXT_COLOR = '#404050';
export const LABELS_COLOR = '#a0a0a0';
