<script>
   /*************************************************************/
   /* Root component for any application                        */
   /* ----------------------------------                        */
   /* universal wrapper for any application                     */
   /* implements routing among tabs/containers                  */
   /* and session management                                    */
   /*************************************************************/

   import { onMount } from 'svelte';
   import { date2str, reviver, replacer } from '../utils';
   import Modal from '../../shared/components/Modal.svelte';

   let {
      WASM,                   // WASM promise from main app
      appID,                  // application ID (as string) is used for saving sessions to local storage
      appTitle,               // application title (for header)
      appShortTitle,          // application short title (for header)
      appSlogan,              // application slogan (for header)
      tabs = [],              // array with tab properties (name, label, component)
      isBeta = false,         // logical is this a beta version or not
      initSession,            // callback which initialize JSON with app session
      checkSession,           // callback which checks validity of the JSON with app session loaded by user
      resetSession,           // callback which leads to app reset via session (when you load a new one)
      info,                   // snippet with HTMl for information window
      footer,                 // snippet with HTML for footer
   } = $props();


   const dbName = 'mdatools-' + appID;                         // database name (appID will be used for storage name)

   let isInfoShown = $state(false);                   // show or not info modal
   let session = $state();                            // curren session
   let sessionMessage = $state('Current session ');   // message to show in session corer
   let sessionHideButtons = $state(false);            // hide or not session buttons

   /* shows session message */
   function showMessage(msg) {
      sessionMessage = msg;
      sessionHideButtons = true;
         setTimeout(() => {
         sessionMessage = 'Current session ';
         sessionHideButtons = false;
      }, 1000);
   }


   /* callback to open modal with app info */
   function showInfo() {
      isInfoShown = true;
   }

   /* callback to close modal with app info */
   function closeInfo() {
      isInfoShown = false;
   }

   /* callback to select tab */
   function selectTab(id) {
      session.tab = id;
   }

   /* callbacl to reset the session in database and in the app window */
   function resetSesstionStorage() {
      const request = indexedDB.open(dbName, 1);
      request.onsuccess = (event) => {
         const db = event.target.result;
         const tx = db.transaction(appID, "readwrite");
         const store = tx.objectStore(appID);
         store.delete("session");

         tx.oncomplete = () => {
            session = initSession();
            showMessage('Session was reinitialzed.');
         };

         tx.onerror = () => console.error("Failed to session from DB.");
      };
   }

   /* callback to save the current session to local storage */
   function backupSession(sessionStr, init) {
      window.localStorage.removeItem(appID + "-blocked");
      const request = indexedDB.open(dbName, 1);
      request.onsuccess = (event) => {
        const db = event.target.result;
        const tx = db.transaction(appID, "readwrite");
        const store = tx.objectStore(appID);
        store.put(sessionStr, "session");
        store.put(date2str(new Date()), "session-update");
      };
   }

   /* callback to restore last session from the local storage */
   function restoreSession() {

      new Promise((resolve, reject) => {
        const request = indexedDB.open(dbName, 1);
        request.onsuccess = (event) => {
            const db = event.target.result;
            const tx = db.transaction(appID, "readonly");
            const store = tx.objectStore(appID);
            const getRequest = store.get("session");
            getRequest.onsuccess = () => resolve(getRequest.result);
            getRequest.onerror = () => reject(getRequest.error);
        };
      })
      .then((data) => {
            setSession(data);
            sessionMessage = 'Last session is restored ';
            sessionHideButtons = true;
            setTimeout(() => {
               sessionMessage = 'Current session ';
               sessionHideButtons = false;
            }, 1500);
      })
      .catch((err) => {
         console.error(err);
         session = initSession();
      });
   }

   /* parse session string to JSON and set it as current app session */
   function setSession(str) {

      if( !str || str === 'undefined') {
         session = initSession();
         return;
      }

      const _session =  JSON.parse(str, reviver);
      session = checkSession(_session) ? _session : initSession(appID);
   }

   /* callback to download session object as JSON file */
   function downloadSession() {

      const dateStr = date2str(new Date());
      const fileName = appID + '-session-' + dateStr + '.json';
      session.date = dateStr;

      const str = JSON.stringify(session, replacer);
      const blob = new Blob([str], { type: 'text/json' });
      const url = window.URL.createObjectURL(blob)
      const a = document.createElement('a')

      a.setAttribute('href', url)
      a.setAttribute('download', fileName);
      a.click()
   }

   /* callback to load session from JSON file */
   function loadSession (e) {
      //resetSession(session);
      const reader = new FileReader();
      reader.onload = () => {
         setSession(reader.result);
         e.target.value = '';
      };
      reader.readAsText(e.target.files[0]);
   }

   function confirmAndReset() {
      if (confirm("Are you sure? All current results and models will be lost in all tabs.")) {
         resetSesstionStorage();
      }
   }

   onMount(() => {
      const storeName = appID;
      const request = indexedDB.open(dbName, 1);

      request.onupgradeneeded = (event) => {
         const db = event.target.result;
         if (!db.objectStoreNames.contains(storeName)) {
            db.createObjectStore(storeName);
         }
      };

      request.onsuccess = (event) => {
         restoreSession();
      }
   });
</script>

{#if session}
<div class="page">

   <header class="page-header">
      <div class="header-group">
         <img class="logo" alt={appShortTitle} src="logo.svg" />
         <h1>{appTitle}{#if isBeta}<sup style="color:red">Î²</sup>{/if}</h1>
         <p>{appSlogan}</p>
      </div>
      <ul class="nav">
         {#each tabs as tab, i}
         <li class="nav-item" class:selected={session.tab === i} >
            <button onclick={() => selectTab(i)} onkeydown={() => selectTab(i)}>{tab.name}</button>
         </li>
         {/each}
      </ul>
   </header>

   <main>
      {#key session.date}
      {#each tabs as tab, i}
      <div class:hidden={session.tab !== i} class="tab-content tab-content-{tab.label}">
      <tab.component {WASM} {session} {backupSession}/>
      </div>
      {/each}
      {/key}
   </main>

   <footer>
      <div class="session" class:no-buttons={sessionHideButtons}>
         <span class="session-message">{sessionMessage}</span>
         <label title="load session from file" class="upload-button"><input type="file" onchange={loadSession}/>
            <svg xmlns="http://www.w3.org/2000/svg" version="2.0" width="20px" height="20px" viewBox="0 0 100 100">
            <line x1="50" y1="25" x2="50" y2="75"/>
            <line x1="30" y1="40" x2="50" y2="25"/>
            <line x1="70" y1="40" x2="50" y2="25"/>
            </svg>
         </label>
         <!-- svelte-ignore a11y_consider_explicit_label -->
         <button title="download session as a file" class="download-button" onclick={downloadSession}>
            <svg xmlns="http://www.w3.org/2000/svg" version="2.0" width="20px" height="20px" viewBox="0 0 100 100">
            <line x1="50" y1="25" x2="50" y2="75"/>
            <line x1="30" y1="60" x2="50" y2="75"/>
            <line x1="70" y1="60" x2="50" y2="75"/>
            </svg>
         </button>
         <!-- svelte-ignore a11y_consider_explicit_label -->
         <button title="reset all sessions" class="clear-button" onclick={confirmAndReset}>
            <svg xmlns="http://www.w3.org/2000/svg" version="2.0" width="20px" height="20px" viewBox="0 0 100 100">
            <line x1="25" y1="25" x2="75" y2="75"/>
            <line x1="25" y1="75" x2="75" y2="25"/>
            </svg>
         </button>
      </div>
      <div class="info">
      <button title="Show information about the app" class="button-info" onclick={showInfo} onkeydown={showInfo}>info</button>
      {@render footer()}
      </div>
   </footer>

   {#if isInfoShown}
   <Modal closeModal={closeInfo}>
      <div class="info-container">
      {@render info()}
      </div>
   </Modal>
   {/if}
</div>
{/if}


