<script>
   /*************************************************************/
   /* AppContainer for creating                                 */
   /* -------------------------                                 */
   /* app container for creating preprocessing chain            */
   /*************************************************************/

   import { untrack } from 'svelte';
   import { replacer } from '../../../shared/utils.js';
   import { prep_fit, prep_apply, prep_trim } from '../../../shared/wasm/dist/prep.js';
   import { Container, Button } from 'svelte-controls-basic';
   import { downloadPrepData, downloadPrepModel, cloneFullData} from '../utils.js';

   import DataLoader from '../shared/DataLoader.svelte';
   import AppPlots from '../shared/AppPlots.svelte';
   import PrepCreator from './PrepCreator.svelte';


   // WASM promise and session object from parent app
   let {
      WASM,
      backupSession,
      session = $bindable()
   } = $props();

   // get part of the session for this tab
   const s = $derived(session['create']);
   let varind = $state([-1, 0, 0]);

   let status = $state('neutral');
   let errMsg = $state('');
   let promise = $state();

   /****************************/
   /* Helper functions         */
   /****************************/

   // fit preprocessing model using prepobject directives.
   async function prepFit(rawdata, prepobj, outliers) {
      if (!prepobj || !rawdata || prepobj.length === 0) return;
      promise = WASM()
         .then(Module => {
            status = 'applying';

            const m = prep_fit(Module, rawdata.values, rawdata.nresp, prepobj, false, outliers);
            const v = prep_apply(Module, rawdata.values, rawdata.nresp, m, false, false);
            const d = cloneFullData($state.snapshot(rawdata), v);

            m.info = s.prepmeta;
            s.prepmodel = m;
            s.prepdata = prep_trim(d, s.prepmodel);

         })
         .catch(e =>{
            // something went wrong
            s.prepmodel = null;
            s.prepdata = null;
            console.error(e);
            status = 'error';
            errMsg = e;
         });
   }

   // callbacks for tracking left and right boundaries
   function setLeft(v) {
      const nvars = s.rawdata.values.ncols - s.rawdata.nresp;
      if (s.rawdata && v >= 0 && v < nvars && v < s.right) {
         s.left = v;
      }
   }

   function setRight(v) {
      const nvars = s.rawdata.values.ncols - s.rawdata.nresp;
      if (s.rawdata && v <=nvars && v > s.left) {
         s.right = v;
      }
   }

   function finalize() {
      prepFit(s.rawdata, s.prepobj, []);
   }

   function reset() {
      s.prepmodel = null;
      s.prepdata = null;
   }

   /****************************/
   /* Effects                  */
   /****************************/

   // effect to track changes in dataset and models
   $effect(() => {
      if (s.rawdata === null) {
         s.left = 0;
         s.right = 0;
         s.prepdata = null;
         s.rawdataShort = null;
         s.prepdataShort = null;
         s.prepmodel = null;
         s.prepobj = [];
         varind = [-1, -1, -1];
      }
   });

   $effect(() => {
      if (!s.prepobj || s.prepobj.length === 0) {
         s.prepdata = null;
         s.prepdataShort = null;
         varind = [-1, -1, -1];
      }
   });

   $effect(() => {
      const sessionStr = untrack(() => JSON.stringify(session, replacer));
      backupSession(sessionStr, s.rawdata, s.prepdata, s.prepdataShort);
   });
</script>

<div class="app-container">

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="data-loader">
         <DataLoader bind:rawdata={s.rawdata} {setLeft} {setRight}/>
      </div>


      {#if s.rawdata }
         <div class="prep-creator">
            <PrepCreator
               {WASM}
               rawdata={s.rawdata}
               left={s.left}
               right={s.right}
               bind:prepmodel={s.prepmodel}
               bind:prepdata={s.prepdata}
               bind:prepdataShort={s.prepdataShort}
               bind:rawdataShort={s.rawdataShort}
               bind:varind={varind}
               bind:prepobj={s.prepobj}
               bind:meta={s.prepmeta}
               {setLeft},
               {setRight}
               {reset}
               {finalize}
            />
         </div>

         <!-- error message -->
         {#if status === 'error' || errMsg}
         <Container status="error">
            <p>{errMsg}</p>
         </Container>
         {/if}

         {#if s.prepmodel && s.prepdata}
         <div class="downloads-block">
            <Container label="Download">
               <Button text="Data" onclick={() => downloadPrepData(s.prepdata, s.left, s.right)}/>
               <Button text="Model" onclick={() => downloadPrepModel(s.prepmodel)}/>
            </Container>
            </div>
         {/if}
      {/if}
   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if !s.rawdata}
         <div class="help-text">
            <p>
               In this tab you can create a chain of preprocessing methods (<em>preprocessing model</em>), tune their order and parameters, and check how your data look like before and after preprocessing. When you done you can dowload the model and the preprocessed data to be used in other apps. All you need is just to load your data from a single CSV file or from one or several SPC files.
            </p>
            <p><strong>CSV file</strong><br>
               CSV file should contain data as numeric values delimited by comma (with dot as decimal separator) or
               by semicolon (with comma as decimal separator). Rows correspond to observations or objects, columns â€” to variables.
            </p>
            <p>
               First row in the CSV file can contain variable labels or values (<strong>header</strong>). It is assumed that variable lables are text (or have text parts), e.g. <em>"Na"</em>, <em>"OD260"</em>, etc. If variables are part of signals, e.g. spectra, the header can content numeric values, e.g. wavelength or wavenumbers. In this case, select <em>values</em> option for the header and you will also be able to specify name of the values (e.g. <em>Wavelength</em>) and units (e.g. <em>nm</em>) if necessary.
            </p>
            <p>
               First column in the file can contain labels for the objects/observations (<strong>row labels</strong>) â€” just set the corresponding options after selecting the file.
            </p>
            <p>If your CSV file contains a column, which is not a part of the main datawith, e.g. with <strong>group labels</strong> (classes, subsets, etc.) or <strong>response values</strong>, you need to set corresponding option in the dialog box. It is assumed that this column is next to the column with row labels or is the first column of row labels are absent.</p>

            <p><strong>SPC files</strong><br>
            You can also load spectral data directly from Galactic Industries SPC data files. The app supports both single spectrum in a file and files with multiple spectra, however only 2-way datasets can be loaded. You can select several SPC files to load (hold Ctrl or Shift when selecting), in this case they will be combined together into the same dataset.</p>

            <p>After the data is loaded you will be able to create and tune the sequence of preprocessing methods. The result will be shown on a plot for 30 randomly selected rows. To apply the methods to whole data and then dowload the preprocessed values and the model, just click <em>Finalize</em> button.</p>
         </div>
      {:else if s.rawdataShort && s.prepdataShort}
         {#key s.rawdata }
         <AppPlots
            rawdata={s.rawdataShort}
            prepdata={s.prepdataShort}
            left={s.left}
            right={s.right}
            {varind}
         />
         {/key}
      {/if}
   </div>
</div>

<style>

   .app-container {
      font-family: Helvetica, Arial, sans-serif;
      font-size: 1rem;
      color: #404040;
      box-sizing: border-box;
      margin: 0 auto;
      padding: 0.35em 0.25em;
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-areas: "controls content";
      grid-template-rows: 100%;
      grid-template-columns: min(310px, 30%) 1fr;
   }

   .app-controls {
      box-sizing: border-box;
      padding: 0.25em 0.5em;
   }

   .app-content {
      box-sizing: border-box;
      padding: 0 1em 0 0.5em;
      padding: 0.25em 0.25em;
      height: 100%;
   }

   .app-controls > div {
      border-radius: 0.25rem;
   }

   .help-text {
      margin-top: 0.25rem;
   }

   .help-text > p {
      background: #fafafa;
      padding: 0em 1.5em 0.5em 1.5em;
      color: #606060;
      line-height: 1.5em;
      margin: 0 1em 0 0;
   }

   .help-text > p:first-child {
      border-top-left-radius: 0.25rem;
      border-top-right-radius: 0.25rem;
      padding-top: 1.5em;
   }

   .help-text > p:last-child {
      border-bottom-left-radius: 0.25rem;
      border-bottom-right-radius: 0.25rem;
      padding-bottom: 1.5em;
   }

   .downloads-block {
      margin-top: .85em;
   }

   .downloads-block > :global(div:hover),
   .downloads-block > :global(div) {
      background: #fff0c8;
   }

   .downloads-block > :global(div button) {
      margin: 0 0.25em;
   }

   .downloads-block > :global(div .button:hover) {
      background: #fffff8;
      border-color: #606570;
      color: #606570;
   }


   @media(min-width:1300px) {
      .app-container {
         grid-template-columns: min(340px, 30%) 1fr;
      }
   }

   @media(min-width:1100px) {
      .app-container {
         grid-template-columns: min(320px, 30%) 1fr;
      }
   }

</style>